import Vue from 'vue'
import Vuex from 'vuex'
import Raven from 'raven-js'
import RavenVue from 'raven-js/plugins/vue'
import VueRouter from 'vue-router'
import Toasted from 'vue-toasted'

import Navbar from 'components/navbar'
import FeatureNotice from 'components/feature-notice'
import Course from 'assets/js/course'
import QuestionBank from './question-bank'
import PaperBank from './paper-bank'
import SecondaryNavbar from './secondary-navbar'
import PlatformDocument from './platform-document'
import Yhfa from './yhfa'
import VideoBank from './video-bank'
import MineIndex from './mine/index'
import MineUploadedFiles from './mine/uploaded-files'
import MineFavoritedFiles from './mine/favorited-files'
import MineNewQuestions from './mine/new-questions'
import MinePaper from './mine/papers'
import NewMinePaper from './mine/new-papers'
import CreateNewQuestion from './create-new-question'
import SchoolResource from './school-resource'
import Zxxk from './zxxk'

require('./index.less')

Vue.config.productionTip = false

const axios = require('axios')

axios.defaults.headers.common.X_REQUESTED_WITH = 'XMLHttpRequest'
axios.defaults.xsrfHeaderName = 'X-CSRFTOKEN'
axios.defaults.xsrfCookieName = 'csrftoken'

if (process.env.NODE_ENV === 'production') {
  Vue.prototype.$raven = Raven
  Raven.config('http://216e3d581cd9424fad7f5d8c046b2f64@sentry.xdf100.com/6').addPlugin(RavenVue, Vue).install()
}

Vue.use(Vuex)
Vue.use(VueRouter)
Vue.use(Toasted, { duration: 1000, position: 'bottom-center' })

const router = new VueRouter({
  mode: 'history',
  base: process.env.NODE_ENV === 'production' ? '/pad/resource' : '/static/w/pages/resource.html',
  routes: [
    { path: '', redirect: '/mine/uploaded-files' },
    { path: '/question-bank', component: QuestionBank, name: 'question-bank' },
    { path: '/zxxk', component: Zxxk, name: 'zxxk' },
    { path: '/paper-bank', component: PaperBank, name: 'paper-bank' },
    { path: '/create-new-question', component: CreateNewQuestion, name: 'create-new-question' },
    { path: '/video-bank', component: VideoBank, name: 'video-bank' },
    { path: '/yhfa', component: Yhfa, name: 'yhfa' },
    { path: '/platform-document', component: PlatformDocument, name: 'platform-document' },
    { path: '/school-resource', component: SchoolResource, name: 'school-resource' },
    {
      path: '/mine',
      component: MineIndex,
      name: 'mine',
      redirect: 'mine/uploaded-files',
      alias: '',
      children: [
        { path: 'uploaded-files', component: MineUploadedFiles, name: 'uploaded-files' },
        { path: 'favorited-files', component: MineFavoritedFiles, name: 'favorited-files' },
        { path: 'new-questions', component: MineNewQuestions, name: 'mine-new-questions' },
        { path: 'new-papers', component: NewMinePaper, name: 'new-mine-papers' },
      ],
    },
  ],
  linkActiveClass: 'active',
})


const store = new Vuex.Store({
  state: {
    teacher: null,
    bucketResources: [], // 里面的每一项是一个 item
    beforeClassDocs: [],
    inClassDocs: [],
    afterClassDocs: [],
    unfoldBucket: true,
    minePageChapterId: 0,
    bucketResourceKey: '',
    uploadActionCount: 0,
  },
  getters: {
    school: state => (state.teacher ? state.teacher.school : null),
    classes: state => (state.teacher ? state.teacher.classes : []),
    bucketIndexOf: state => (item) => {
      for (let i = 0, len = state.bucketResources.length; i < len; i++) {
        const bucketItem = state.bucketResources[i]
        if (item.id === bucketItem.id && item.kind === bucketItem.kind) {
          return i
        }
      }
      return -1
    },
    checkInBucket: (state, getters) => item => getters.bucketIndexOf(item) >= 0,
    bucketResourceCount: state => state.bucketResources.length,
    gradesOptions: state => state.teacher && state.teacher.gradeIds || [],
  },
  mutations: {
    uploadActionIncrease(state) {
      state.uploadActionCount++
    },
    updateMinePageChapterId(state, cid) {
      state.minePageChapterId = cid
    },
    toggleBucket(state) {
      state.unfoldBucket = !state.unfoldBucket
    },

    // 下面三个方法在课前课中课后 docs 之间拖动会用到，不涉及 bucket 的变化
    updateBeforeClassDocs(state, items) {
      state.beforeClassDocs = items
    },
    updateInClassDocs(state, items) {
      state.inClassDocs = items
    },
    updateAfterClassDocs(state, items) {
      state.afterClassDocs = items
    },

    // bucket 的更改一定会影响 课前课中课后 docs 里面的变化
    bucketResourcesReplace(state, items) {
      state.bucketResources = items
      state.beforeClassDocs = []
      state.inClassDocs = []
      state.afterClassDocs = []
      for (const i of items) {
        state.inClassDocs.push(i)
      }
      localStorage.setItem(state.bucketResourceKey, JSON.stringify(items))
    },
    bucketResourcesAdd(state, item) {
      state.bucketResources.push(item)
      state.inClassDocs.push(item)
      localStorage.setItem(state.bucketResourceKey, JSON.stringify(state.bucketResources))
    },
    bucketResourcesRemove(state, payload) {
      const index = payload.index
      const item = payload.item

      state.bucketResources.splice(index, 1)
      localStorage.setItem(state.bucketResourceKey, JSON.stringify(state.bucketResources))

      for (let i = 0, len = state.inClassDocs.length; i < len; i++) {
        const item_ = state.inClassDocs[i]
        if (item_.id === item.id && item_.kind === item.kind) {
          state.inClassDocs.splice(i, 1)
          return
        }
      }
      for (let i = 0, len = state.beforeClassDocs.length; i < len; i++) {
        const item_ = state.inClassDocs[i]
        if (item_.id === item.id && item_.kind === item.kind) {
          state.beforeClassDocs.splice(i, 1)
          return
        }
      }
      for (let i = 0, len = state.afterClassDocs.length; i < len; i++) {
        const item_ = state.afterClassDocs[i]
        if (item_.id === item.id && item_.kind === item.kind) {
          state.afterClassDocs.splice(i, 1)
          return
        }
      }
    },
    updateTeacher(state, payload) {
      state.teacher = payload.teacher
      const courses = []
      payload.teacher.courses.forEach(courseData => {
        courses.push(new Course(courseData))
      })
      state.teacher.courses = courses
      // 可以看到作业的教学班级
      const smCourses = []
      payload.teacher.smCourses.forEach(courseData => {
        smCourses.push(new Course(courseData))
      })
      state.teacher.smCourses = smCourses

      state.bucketResourceKey = `${state.teacher.id}//-` + 'bucketResources'
    },
    injectCourseStudents(state, payload) {
      for (const c of state.teacher.courses) {
        if (c.id !== payload.courseId) {
          continue
        }
        c.injectStudentData(payload.studentsData)
      }
      for (const c of state.teacher.smCourses) {
        if (c.id !== payload.courseId) {
          continue
        }
        c.injectStudentData(payload.studentsData)
      }
    },
    injectCourseGroups(state, payload) {
      for (const c of state.teacher.courses) {
        if (c.id !== payload.courseId) {
          continue
        }
        c.injectGroupData(payload.groupsData)
      }
      for (const c of state.teacher.smCourses) {
        if (c.id !== payload.courseId) {
          continue
        }
        c.injectGroupData(payload.groupsData)
      }
    },
  },
  actions: {
    initBucketResources({ state, commit }) {
      const localBucketResources = JSON.parse(localStorage.getItem(state.bucketResourceKey)) || []
      commit('bucketResourcesReplace', localBucketResources)
    },
    addInBucket({ commit, getters }, item) {
      if (!getters.checkInBucket(item)) {
        commit('bucketResourcesAdd', item)
      }
    },
    removeFromBucket({ commit, getters }, item) {
      const index = getters.bucketIndexOf(item)
      if (index < 0) return
      commit('bucketResourcesRemove', { index, item })
    },
    clearBucket({ commit }) {
      commit('bucketResourcesReplace', [])
    },
  },
})


router.beforeEach((to, from, next) => {
  if (!store.state.teacher) {
    axios.get('/pad/j/teacher_info').then((response) => {
      const teacher = response.data.teacher
      store.commit('updateTeacher', { teacher })
      if (process.env.NODE_ENV === 'production') {
        Raven.setUserContext({
          username: teacher.name,
          id: teacher.id,
        })
      }
      next()
    })
  } else {
    next()
  }
});

(async () => {
  process.env.NODE_ENV === 'development' && await import(/* webpackChunkName: "mock" */ '@/mock')
  new Vue({
    delimiters: ['${', '}'],
    el: '#app',
    store,
    router,
    data: {},
    components: { Navbar, SecondaryNavbar, FeatureNotice },
  })
})()




