<template>
  <nav class="secondary-nav">
    <div class="ui secondary pointing blue menu">
      <router-link to="/platform-document" tag="a" class="item">平台资源</router-link>
      <router-link to="/video-bank" tag="a" class="item">名师视频/微课</router-link>
      <router-link to="/yhfa" tag="a" class="item" v-if="showYhfa">优化方案</router-link>
      <!-- <router-link to="/question-bank" tag="a" class="item">题库/组卷</router-link> -->
      <router-link to="/zxxk" tag="a" class="item">学科网</router-link>
      <router-link v-if="teacher && teacher.gradeId===2" to="/paper-bank" tag="a" class="item">试卷库</router-link>
      <div class="right menu">
        <router-link :to="{name: 'school-resource'}" tag="a" class="item">校本资源</router-link>
        <router-link :to="{name: 'mine'}" tag="a" class="item">我的</router-link>
        <div class="ui dropdown item">
          <i class="add square icon"></i>
          上传/创建资源
          <div class="menu">
            <a class="item" @click="openUploader=true">上传文件</a>
            <a class="item"
              :href="pathConvert('question-bank', 'gallery')"
              v-if="displayNewQuestionBank"
              >题库/组卷</a>
            <router-link to="/create-new-question" tag="a" class="item">新建互动资料</router-link>
          </div>
        </div>
      </div>
    </div>
    <file-uploader v-if="openUploader" @close="openUploader=false"></file-uploader>
  </nav>
</template>

<script>
import { mapState } from 'vuex'
import { NORMAL_SUBJECTS, NEW_BANK_ACCESSABLE_SUBJECT_IDS } from 'assets/js/consts/common'
import FileUploader from './file-uploader'
import { pathConvert } from '@/assets/js/utils'

export default {
  name: 'secondary-navbar',
  data() {
    return {
      openUploader: false,
      pathConvert,
    }
  },
  components: { FileUploader },
  mounted() {
    $(this.$el)
      .find('.ui.dropdown')
      .dropdown({
        action: 'nothing',
        on: 'hover',
      })
  },
  computed: {
    showYhfa() {
      return (
        this.teacher &&
        this.teacher.gradeId === 2 &&
        NORMAL_SUBJECTS.hasOwnProperty(this.teacher.subjectId)
      )
    },
    displayNewQuestionBank() {
      if (!this.teacher) return false
      return NEW_BANK_ACCESSABLE_SUBJECT_IDS.includes(this.teacher.subjectId)
    },
    ...mapState(['teacher']),
  },
}
</script>

<style lang="less" scoped>
@import "~assets/css/base-variables.less";
.secondary-nav {
  background-color: @color-white;
  border-bottom: 1px solid #e0e0e0;
  .ui.secondary.pointing.menu {
    width: 55rem;
    margin: 0 auto;
    border-bottom-color: transparent;
    height: 40px;
    font-size: 13px;
    .item {
      color: #888;
    }
    .dropdown.item {
      .add.icon {
        color: @color-teal;
      }
    }
    .dropdown.item>.menu {
      margin-top: 0;
      border-radius: 0;
      border: solid 1px @color-teal;
    }
  }
}
.ui.blue.menu .active.item, .ui.menu .blue.active.item {
  border-color: transparent !important;
}
</style>



