<template>
  <div class="platform-document" id="platform-document">
    <div class="sidebar-tree">
      <div>
        <span class="text-muted">根据章节筛选</span>
        <chapter-selector key="sidebar-mine-selector" class="sidebar-selector"
          bookSource="platform"
          @selectChapter="cid=>$store.commit('updateMinePageChapterId', cid)"
          :initFirstSelected="false"
          ></chapter-selector>
      </div>
      <resource-bucket class="component-resource-bucket"></resource-bucket>
    </div>
    <div class="document-container" id="document-container">
      <div class="ui container-tab-menu menu">
        <router-link :to="{name: 'uploaded-files'}" tag="a" class="item">我上传的文件</router-link>
        <router-link :to="{name: 'favorited-files'}" tag="a" class="item">我收藏的文件</router-link>
        <router-link :to="{name: 'mine-new-questions'}" tag="a" class="item">我的互动资料</router-link>
        <router-link
          :to="{name: 'new-mine-papers'}"
          tag="a"
          v-if="displayNewQuestionBank"
          class="item">我的试题组</router-link>
      </div>
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import ChapterSelector from 'components/chapter-selector'
import { NEW_BANK_ACCESSABLE_SUBJECT_IDS } from '@/assets/js/consts/common'
import ResourceBucket from '../resource-bucket'

export default {
  name: 'mine-index',
  props: {},
  data() {
    return {
      loading: false,
      usageType: 0,
      fileType: 0,
      documents: [],
      totalPage: 0,
      selectedPage: 1,
    }
  },
  components: {
    ChapterSelector, ResourceBucket,
  },
  computed: {
    displayNewQuestionBank() {
      if (!this.teacher) return false
      return NEW_BANK_ACCESSABLE_SUBJECT_IDS.includes(this.teacher.subjectId)
    },
    ...mapState(['teacher']),
  },
  mounted() {
    this.$store.commit('updateMinePageChapterId', 0)
    $(window).scroll(() => {
      const windowTop = $(window).scrollTop()
      if (windowTop > 118) {
        $('.sidebar-tree').addClass('fixed')
      }
      if (windowTop < 118) {
        $('.sidebar-tree').removeClass('fixed')
      }
    })
  },
}
</script>

<style lang="less" scoped>
.platform-document {
  .sidebar-tree, .document-container {
    display: inline-block;
    background-color: white;
    box-shadow: 0 1px 1px rgba(0,0,0,0.15);
  }
  position: relative;
  .sidebar-tree {
    z-index: 10;
    height: 100vh;
    width: 25rem;
    position: absolute;
    padding: .5rem;
    .sidebar-selector {
      height: 85vh;
    }
    .component-resource-bucket {
      box-shadow: -2px -3px 3px rgba(0,0,0,0.15);
      position: fixed;
      width: 25rem;
      margin-left: -.5rem;
      bottom: 0;
    }
  }
  .sidebar-tree.fixed {
    position: fixed;
    top: 0;
  }
  .document-container {
    position: relative;
    min-height: 100vh;
    margin-left: 26rem;
    width: ~"calc(100% - 26rem)"
  }
}
</style>



