<template>
  <div class="school-resource" id="school-resource">
    <div class="sidebar-tree">
      <div>
        <span class="text-muted">根据章节筛选</span>
        <chapter-selector
          key="sidebar-school-selector"
          class="sidebar-school-selector"
          bookSource="platform"
          @selectChapter="cid=>$store.commit('updateMinePageChapterId', cid)"
          :initFirstSelected="false"
          ></chapter-selector>
      </div>
      <resource-bucket class="component-resource-bucket"></resource-bucket>
    </div>
    <div class="document-container" id="document-container">
      <h2 class="container-title">校本资源</h2>
      <span class="text-muted note">只展示你所教学科和学段的校本资源</span>
      <!--
      <div class="pill-filter">
        用途：
        <span class="filter-label" v-for="(name, key) in docUsageNameMap"
          :class="usageType===parseInt(key)?'active':''" @click="usageType=parseInt(key)">{{name}}</span>
      </div>
      -->
      <div class="pill-filter">
        类型：
        <span class="filter-label" v-for="obj of kinds"
          :class="kind===parseInt(obj.kind)?'active':''" @click="kind=parseInt(obj.kind)">{{obj.name}}
        </span>
      </div>
      <resource-list
        :items="items"
        :loading="loading"
        :totalPage="totalPage"
        :curPage="selectedPage"
        @update:selected="page=>fetchDocuments(page)"
        :showChapter="true"
        :showSize="kind===2030 || kind===2025"></resource-list>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import { mapState } from 'vuex'

import { DOC_USAGE_NAME_MAP } from 'assets/js/consts/common'
import ChapterSelector from 'components/chapter-selector'
import ResourceBucket from './resource-bucket'
import ResourceList from './resource-list'

export default {
  name: 'school-resource',
  props: {},
  data() {
    return {
      loading: false,
      usageType: 0,
      items: [],
      totalPage: 0,
      selectedPage: 1,
      docUsageNameMap: DOC_USAGE_NAME_MAP,
      kinds: [
        { kind: 2030, name: '文件' },
        { kind: 2025, name: '互动资料' },
        { kind: 2035, name: '试题组' },
      ],
      kind: 2030,
    }
  },
  components: {
    ChapterSelector, ResourceList, ResourceBucket,
  },
  mounted() {
    this.$store.commit('updateMinePageChapterId', 0)
    this.fetchDocuments()
    $(window).scroll(() => {
      const windowTop = $(window).scrollTop()
      if (windowTop > 118) {
        $('.sidebar-tree').addClass('fixed')
      }
      if (windowTop < 118) {
        $('.sidebar-tree').removeClass('fixed')
      }
    })
  },
  watch: {
    usageType() { this.fetchDocuments() },
    kind() { this.fetchDocuments() },
    minePageChapterId() { this.fetchDocuments() },
    $route: 'fetchDocuments',
  },
  computed: {
    ...mapState(['minePageChapterId']),
  },
  methods: {
    fetchDocuments(page = 1) {
      this.loading = true
      const url = '/pad/j/resource/school_shared'
      return axios.get(url, { params: {
        page,
        pageSize: 15,
        chapterId: this.minePageChapterId,
        usage: this.usageType,
        kind: this.kind,
      } }).then((response) => {
        if (response.data.status === 0) {
          this.items = response.data.items
          this.totalPage = response.data.totalPage
          this.selectedPage = page
          this.loading = false
        } else if (response.data.detail) {
          this.$toasted.error(response.data.detail, {
            duration: 1000,
            position: 'top-center',
          })
          this.loading = false
        }
      })
    },
  },
}
</script>

<style lang="less" scoped>
.school-resource {
  .sidebar-tree, .document-container {
    display: inline-block;
    background-color: white;
    box-shadow: 0 1px 1px rgba(0,0,0,0.15);
  }
  position: relative;
  .sidebar-tree {
    z-index: 10;
    height: 100vh;
    width: 25rem;
    position: absolute;
    padding: .5rem;
    .sidebar-school-selector {
      height: 85vh;
    }
    .component-resource-bucket {
      box-shadow: -2px -3px 3px rgba(0,0,0,0.15);
      position: fixed;
      width: 25rem;
      margin-left: -.5rem;
      bottom: 0;
    }
  }
  .sidebar-tree.fixed {
    position: fixed;
    top: 0;
  }
  .document-container {
    position: relative;
    min-height: 100vh;
    margin-left: 26rem;
    width: ~"calc(100% - 26rem)"
  }
}
.note {
  font-size: 12px;
  font-weight: normal;
  margin-left: 2rem;
}
</style>



