<template>
  <div class="create-new-question ui form" id="create-new-question">
    <div class="sidebar-file">
      <loader v-if="fileLoading"></loader>
      <div class="content-empty" v-if="!fileLoading && !materialFileKey && !analysisFileKey">
        <img :src="require('assets/img/content-empty.png')">
        <p class="text-muted">文件预览区域</p>
      </div>
      <component
        v-if="!fileLoading && (materialFileKey || analysisFileKey)"
        :is="currentView.type"
        v-bind="currentView.props"></component>
    </div>
    <div class="new-question-container preview" v-if="preview">
      <span class="actions">
        <button class="ui tiny teal button" @click="preview=false">返回编辑</button>
        <button class="ui tiny primary button" @click="openCreateNqModal()">下一步</button>
      </span>
      <h2 class="container-title">新建互动资料 <span class="note" v-if="questions.length">总分 {{totalScore}} 分</span></h2>
      <div class="question-list">
        <div class="question-block" v-for="(question, index) of questions">
          <span class="label">{{index+1}}.</span>
          <span class="question-kind-label">{{question.kindName}}</span>
          <span class="text-muted">({{question.score}}分) {{question.text}}</span>
          <div v-if="question.type!==2027" class="seq-label-list">
            <span v-for="optionSeq in question.optionCount" :class="outlineOptionClass(question, optionSeq)" @click="toggleChecked(question, optionSeq)">
              {{answerLabelOf(optionSeq, question.isTrueOrFalse)}}
            </span>
          </div>
          <div v-else>
            <button class="ui basic large icon button"> <i class="ui photo icon"></i> </button>
          </div>
        </div>
      </div>
    </div>
    <div class="new-question-container" v-else>
      <span class="actions" v-if="questions.length">
        <button class="ui tiny teal button" @click="preview=true">预览</button>
        <button class="ui tiny primary button" @click="openCreateNqModal()">下一步</button>
      </span>
      <h2 class="container-title">新建互动资料 <span class="note" v-if="questions.length">总分 {{totalScore}} 分</span></h2>

      <p class="note">1. 上传文件： <span class="text-muted">支持doc, docx, pdf, jpg, jpeg, bmp, png, mp3, mp4格式。</span></p>
      <div class="add-files">
        <div class="file-row">
          <template v-if="materialFileKey">
            <span class="file-uploaded-label">材料文件已上传 <i class="teal check circle icon"></i></span>
            <span class="text-muted note" v-if="previewItemName==='material'">预览中</span>
            <span class="link link-primary" v-else @click="previewItemName='material'">点击预览</span>
            <span class="link link-danger" @click="deleteMaterialFile">删除文件重新上传</span>
          </template>
          <span class="file-input-wrapper" v-show="!materialFileKey">
            <button class="ui tiny primary button">上传材料文件</button>
            <label for="btn-add-material-file"></label>
            <input type="file" id="btn-add-material-file" @click="e => e.target.value = null">
            <span class="text-muted note">学生答题时阅读此文件，并在答题卡上作答。必需文件。</span>
            <span class="text-muted progress"></span>
          </span>
        </div>
        <div class="file-row">
          <template v-if="analysisFileKey">
            <span class="file-uploaded-label">解析文件已上传 <i class="teal check circle icon"></i></span>
            <span class="text-muted note" v-if="previewItemName==='analysis'">预览中</span>
            <span class="link link-primary" v-else @click="previewItemName='analysis'">点击预览</span>
            <span class="link link-danger" @click="deleteAnalysisFile">删除文件重新上传</span>
          </template>
          <span class="file-input-wrapper" v-show="!analysisFileKey">
            <button class="ui tiny default button">上传解析文件</button>
            <label for="btn-add-analysis-file"></label>
            <input type="file" id="btn-add-analysis-file" @click="e => e.target.value = null">
            <span class="text-muted note">学生交卷后查看此文件，与发送设置的「开放答案」配合。可选文件。</span>
            <span class="text-muted progress"></span>
          </span>
        </div>
      </div>

      <p class="note">2. 在下方添加答题卡</p>
      <div class="field question-list">
        <div class="question-block" v-for="(question, index) of questions">
          <div class="ui mini menu">
            <span class="item kind-name">{{index+1}}. {{question.kindName}}</span>
            <a class="item">
              <input type="number" v-model.number="question.score" v-on:input="($event)=>changeQuestionScore(question, $event)" min="0" max="100">分
            </a>
            <div class="right menu">
              <template v-if="question.type === 2026 && !question.isTrueOrFalse">
                <a class="item" @click="pushOption(question)" :class="canPushOption(question)?'':'disabled'">
                  <i class="add icon"></i>选项
                </a>
                <a class="item" @click="popOption(question)" :class="canPopOption(question)?'':'disabled'">
                  <i class="minus icon"></i>选项
                </a>
              </template>
              <a class="item" :class="index===0?'disabled':''" @click="qForward(question)">
                <i class="arrow up icon"></i>上移
              </a>
              <a class="item" :class="index===questions.length-1?'disabled':''" @click="qBackward(question)">
                <i class="arrow down icon"></i> 下移
              </a>
              <a class="item" @click="qDelete(question)">
                <i class="trash outline icon"></i>
              </a>
            </div>
          </div>
          <input type="text" class="question-text" v-model="question.text" placeholder="题干（可不填）">
          <div v-if="question.kind!==2027" class="seq-label-list">
            <span v-for="optionSeq in question.optionCount" :class="outlineOptionClass(question, optionSeq)" @click="toggleChecked(question, optionSeq)">
              {{answerLabelOf(optionSeq, question.isTrueOrFalse)}}
            </span>
          </div>
        </div>
      </div>
      <div class="add-questions">
        <div class="add-question">
          <button class="ui tiny button" @click="addSingleChoice"><i class="add icon"></i>单选题</button>
          <p class="text-muted">默认<input type="number" v-model.number="singleChoiceScore" min="0" max="100">分</p>
        </div>
        <div class="add-question">
          <button class="ui tiny button" @click="addMultiChoice"><i class="add icon"></i>多选题</button>
          <p class="text-muted">默认<input type="number" v-model.number="multiCHoiceScore" value="5" min="0" max="100">分</p>
        </div>
        <div class="add-question">
          <button class="ui tiny button" @click="addJudgement"><i class="add icon"></i>判断题</button>
          <p class="text-muted">默认<input type="number" v-model.number="judgementScore" value="5" min="0" max="100">分</p>
        </div>
        <div class="add-question">
          <button class="ui tiny button" @click="addSubjective"><i class="add icon"></i>主观题</button>
          <p class="text-muted">默认<input type="number" v-model.number="subjectiveScore" value="10" min="0" max="100">分</p>
        </div>
      </div>
    </div>
    <modal v-if="createNqModal" @close="createNqModal=false" class="modal-large modal-create-new-question" key="modal-create-new-question">
      <h3 slot="header">新建互动资料</span></h3>
      <div slot="body">
        <div class="modal-body-left">
          <div class="field required">
            <label>名称</label>
            <input type="text" v-model="title" placeholder="不超过100个字符">
          </div>
          <div class="field required">
            <label>年级</label>
            <div class="ui selection dropdown">
              <i class="dropdown icon"></i>
              <span class="text" v-if="gradeId">{{grades[gradeId]}}</span>
              <span class="default text" v-else>选择年级</span>
              <div class="menu">
                <div class="item" v-for="gid in gradesOptions" @click="gradeId=gid">{{grades[gid]}}</div>
              </div>
            </div>
          </div>
          <div class="field inline">
            <label>关联章节（可不填）</label>
            <chapter-selector bookSource="platform" key="platform-chapter-selector" @selectChapter="cid=>chapterId=cid"></chapter-selector>
          </div>
        </div>
      </div>
      <div slot="footer">
        <button class="ui tiny button" @click="createNqModal=false">取消</button>
        <button class="ui tiny blue button" @click="createNq">确定</button>
      </div>
    </modal>
  </div>
</template>

<script>
import axios from 'axios'
import qs from 'qs'
import { mapState, mapGetters } from 'vuex'
import Modal from 'components/modal'
import Loader from 'components/loader'
import pageDocument from 'components/doc/office'
import pageImage from 'components/doc/image'
import pageVideo from 'components/doc/video'
import pageAudio from 'components/doc/audio'
import ChapterSelector from 'components/chapter-selector'
import { getQuestionByData } from 'assets/js/questions/view-mode'
import { EXT2FILECOMPONENT, GRADES, OSS_REGION, OSS_BUCKET, FILE_TYPE_EXT_MAP } from 'assets/js/consts/common'


export default {
  name: 'create-new-question',
  props: {},
  data() {
    return {
      message: '',
      title: '',
      grades: GRADES,
      gradeId: 0,
      questions: [],
      singleChoiceScore: 5,
      multiCHoiceScore: 5,
      judgementScore: 5,
      subjectiveScore: 10,
      preview: false,
      createNqModal: false,
      chapterId: 0,
      materialFileKey: '',
      materialFileType: 0,
      analysisFileKey: '',
      analysisFileType: 0,
      previewItemName: '',
      fileLoading: false,
      copyFromNq: false,
    }
  },
  components: { Modal, pageDocument, pageImage, pageVideo, pageAudio, ChapterSelector, Loader },
  created() {
    const { sourceFileKey } = this.$route.query
    if (sourceFileKey) {
      this.fileLoading = true
      axios.get(`http://${OSS_BUCKET}.oss-cn-beijing.aliyuncs.com/${sourceFileKey}`).then(resp => {
        if (resp.status !== 200) {
          this.$toast.error('作为材料文件的源文件地址错误。')
        } else {
          this.materialFileKey = sourceFileKey
          this.materialFileType = Number(this.$route.query.sourceFileType)
          this.previewItemName = 'material'
        }
        this.fileLoading = false
      })
      return
    }
    let copyFromNqId = Number(this.$route.query.copyFromNqId || 0)

    if (copyFromNqId) {
      axios.get('/pad/j/fetch/new_question', { params: { newQuestionId: copyFromNqId } }).then(response => {
        if (response.data.status === 1) {
          this.$toasted.error(response.data.detail)
        } else if (response.data.status === 0) {
          const nq = response.data.nq
          this.gradeId = nq.gradeId
          this.chapterId = nq.chapterId
          this.materialFileKey = this.extractFileKeyFromUrl(nq.fileUrl)
          this.materialFileType = nq.fileType
          this.title = nq.name
          if (this.materialFileKey) {
            this.previewItemName='material'
          }
          this.analysisFileKey = this.extractFileKeyFromUrl(nq.analysisFileUrl)
          this.analysisFileType = nq.analysisFileType

          this.questions = []
          for (let questionData of nq.questions) {
            const q = getQuestionByData(questionData)
            questionData.type = questionData.kind
            questionData.text = questionData.note
            questionData.kindName = q.kindName
            if ('answer' in questionData) {
              questionData.answer = String(questionData.answer)
            }
            this.questions.push(questionData)
          }
          this.copyFromNq = true
        }
      })
    }
  },
  mounted() {
    $(window).scroll(() => {
      const windowTop = $(window).scrollTop()
      if (windowTop >= 118) {
        $('.sidebar-file').addClass('fixed')
      }
      if (windowTop < 118) {
        $('.sidebar-file').removeClass('fixed')
      }
    })
    this.addUploaderEventListener()
  },
  watch: {
    singleChoiceScore(val) {
      if (val <= 0) this.singleChoiceScore = 1
      if (val >= 100) this.singleChoiceScore = 99
    },
    multiCHoiceScore(val) {
      if (val <= 0) this.multiCHoiceScore = 1
      if (val >= 100) this.multiCHoiceScore = 99
    },
    judgementScore(val) {
      if (val <= 0) this.judgementScore = 1
      if (val >= 100) this.judgementScore = 99
    },
    subjectiveScore(val) {
      if (val <= 0) this.subjectiveScore = 1
      if (val >= 100) this.subjectiveScore = 99
    },
    createNqModal(val) {
      if (val) {
        this.$nextTick().then(() => {
          $(this.$el).find('.ui.dropdown').dropdown({ action: 'hide' })
        })
      }
    },
  },
  computed: {
    totalScore() {
      let score = 0
      for (const q of this.questions) {
        score += q.score
      }
      return score
    },
    currentView() {
      if (this.previewItemName === 'material') {
        const ext = FILE_TYPE_EXT_MAP[this.materialFileType][0]
        return {
          type: EXT2FILECOMPONENT[ext] || null,
          props: {
            fileUrl: `http://${OSS_BUCKET}.oss-cn-beijing.aliyuncs.com/${this.materialFileKey}`,
            key: this.previewItemName,
          },
        }
      } else if (this.previewItemName === 'analysis') {
        const ext = this.analysisFileKey.split('.').pop().toLowerCase()
        return {
          type: EXT2FILECOMPONENT[ext] || null,
          props: {
            fileUrl: `http://${OSS_BUCKET}.oss-cn-beijing.aliyuncs.com/${this.analysisFileKey}`,
            key: this.previewItemName,
          },
        }
      }
    },
    ...mapState(['teacher']),
    ...mapGetters(['gradesOptions']),
  },
  methods: {
    extractFileKeyFromUrl(fileUrl) {
      if (!fileUrl) return ''
      const urlRe = /^(http[s]?:\/\/)?([\w\.-]+)\/(.*)\/?$/
      const matches = fileUrl.match(urlRe)
      if (!(matches && matches.length === 4)) {
        this.$toasted.error('文件地址匹配失败')
      }
      return matches[3]
    },
    addUploaderEventListener() {
      const addMaterialFInput = this.$el.querySelector('#btn-add-material-file')
      const addAnalysisFInput = this.$el.querySelector('#btn-add-analysis-file')

      addMaterialFInput.addEventListener('change', (event) => {
        this.uploadFileToTmpfor(event, 'materialFileKey')
      })
      addAnalysisFInput.addEventListener('change', (event) => {
        this.uploadFileToTmpfor(event, 'analysisFileKey')
      })
    },
    handleAliOssException(error, button) {
      if (error.name === 'RequestTimeTooSkewedError') {
        this.$toasted.error('请求发起的时间有错。请确保电脑本地时间处于校准状态。', {
          duration: 2000,
          position: 'bottom-center',
        })
      } else {
        this.$toasted.error(error, { duration: 2000, position: 'bottom-center' })
      }
      button.classList.remove('loading')
    },
    uploadFileToTmpfor(event, fileReference) {
      const files = event.target.files
      if (!files.length) return
      if (files.length > 1) {
        this.$toasted.error('只能选择上传一个文件', { duration: 800, position: 'bottom-center' })
        return
      }

      const button = event.target.parentElement.querySelector('button')
      const note = event.target.parentElement.querySelector('.note')
      const progress = event.target.parentElement.querySelector('.progress')
      const f = files[0]
      const ext = f.name.split('.').pop().toLowerCase()
      if (['jpg', 'jpeg', 'png', 'bmp', 'mp3', 'mp4', 'pdf', 'doc', 'docx'].indexOf(ext) < 0) {
        this.$toasted.error('文件格式不支持')
        return
      }

      axios.get('/pad/j/misc/aliyun/sts').then(resp => {
        if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail, { duration: 2000, position: 'bottom-center' })
          return
        }
        const ossBucket = new OSS.Wrapper({ region: OSS_REGION, bucket: OSS_BUCKET, ...resp.data.sts })

        const now = new Date()
        const prefix = `media/pad/nq-temp/${now.getFullYear()}/${now.getMonth() + 1}/${now.getDate()}/`
        const fileKey = `${prefix + Math.random().toString(36).substr(2, 15)}.${ext}`

        button.classList.add('loading')
        $(note).hide()

        ossBucket.multipartUpload(fileKey, f, {
          partSize: 1 * 1024 * 128,
          *progress(p) {
            progress.textContent = `${parseInt(p * 100)}%..`
          },
        }).then((response) => {
          if (response.res.status === 200) {
            this[fileReference] = fileKey

            for (const fileType in FILE_TYPE_EXT_MAP) {
              if (FILE_TYPE_EXT_MAP[fileType].includes(ext)) {
                this[fileReference.replace('Key', 'Type')] = fileType
                break
              }
            }

            if (this.previewItemName === '') this.previewItemName = fileReference.slice(0, -7)
            button.classList.remove('loading')
            $(note).show()
            progress.textContent = ''
          }
        }).catch(error => this.handleAliOssException(error, button))
      }).catch(error => this.handleAliOssException(error, button))
    },
    deleteAnalysisFile() {
      this.analysisFileKey = null
      this.analysisFileType = null
      if (this.previewItemName === 'analysis') {
        this.previewItemName = ''
      }
    },
    deleteMaterialFile() {
      this.materialFileKey = null
      this.materialFileType = null
      if (this.previewItemName === 'material') {
        this.previewItemName = ''
      }
    },
    openCreateNqModal() {
      if (!this.questions.length) {
        this.$toasted.error('互动资料必须包含题目，请添加题。', { duration: 1000, position: 'top-center' })
        this.preview = false
        return
      }
      if (!this.materialFileKey) {
        this.$toasted.error('请上传材料文件', { duration: 1000, position: 'top-center' })
        return
      }
      for (let index = 0, len = this.questions.length; index < len; index++) {
        const q = this.questions[index]
        if (q.type === 2026 && q.answer.length <= 0) {
          this.$toasted.error(`请为第${index + 1}题设置答案`, { duration: 1000, position: 'top-center' })
          this.preview = false
          return
        }
      }
      this.createNqModal = true
    },
    canPushOption: q => q.optionCount < 9,
    canPopOption: q => q.optionCount > 2,
    pushOption(q) {
      if (this.canPushOption(q)) q.optionCount++
    },
    popOption(q) {
      if (this.canPopOption(q)) {
        const optionSeqStr = `${q.optionCount}`
        const index = q.answer.indexOf(optionSeqStr)
        if (index >= 0) {
          q.answer = q.answer.slice(0, index) + q.answer.slice(index + 1)
          if (!q.multiAnswer) q.answer = '1'
        }
        q.optionCount--
      }
    },
    toggleChecked(question, optionSeq) {
      const optionSeqStr = `${optionSeq}`
      const index = question.answer.indexOf(optionSeqStr)
      if (question.multiAnswer) {
        if (index >= 0) {
          question.answer = question.answer.slice(0, index) + question.answer.slice(index + 1)
        } else {
          question.answer += optionSeqStr
        }
      } else {
        question.answer = optionSeqStr
      }
    },
    outlineOptionClass(question, optionSeq) {
      const classes = ['seq-label-outline']
      if (question.multiAnswer) {
        classes.push('seq-label-square')
      }
      if (question.answer.indexOf(optionSeq) >= 0) {
        classes.push('option-correct')
      }
      return classes.join(' ')
    },
    answerLabelOf(answer, isTrueOrFalse = false) {
      if (!isTrueOrFalse) {
        return String.fromCharCode(64 + answer)
      }
      return answer === 1 ? '对' : '错'
    },
    changeQuestionScore(question, event) {
      const score = event.target.value
      if (score <= 0) {
        event.target.value = 1
      } else if (score >= 100) {
        event.target.value = 99
      }
      question.score = Number(event.target.value)
    },
    qForward(question) {
      const index = this.questions.indexOf(question)
      if (index === 0) return
      this.questions.splice(index, 1)
      this.questions.splice(index - 1, 0, question)
    },
    qBackward(question) {
      const index = this.questions.indexOf(question)
      if (index === this.questions.length - 1) return
      this.questions.splice(index, 1)
      this.questions.splice(index + 1, 0, question)
    },
    qDelete(question) {
      const index = this.questions.indexOf(question)
      if (index >= 0) this.questions.splice(index, 1)
    },
    addSingleChoice() {
      const q = {
        score: this.singleChoiceScore,
        optionCount: 4,
        multiAnswer: false,
        text: '',
        type: 2026,
        answer: '1',
        kindName: '单选题',
      }
      this.questions.push(q)
      this.$nextTick().then(() => {
        $(window).scrollTop(document.body.scrollHeight)
      })
    },
    addMultiChoice() {
      const q = {
        score: this.multiCHoiceScore,
        optionCount: 4,
        multiAnswer: true,
        text: '',
        type: 2026,
        answer: '',
        kindName: '多选题',
      }
      this.questions.push(q)
      this.$nextTick().then(() => {
        $(window).scrollTop(document.body.scrollHeight)
      })
    },
    addJudgement() {
      const q = {
        score: this.judgementScore,
        optionCount: 2,
        isTrueOrFalse: true,
        text: '',
        type: 2026,
        answer: '1',
        kindName: '判断题',
      }
      this.questions.push(q)
      this.$nextTick().then(() => {
        $(window).scrollTop(document.body.scrollHeight)
      })
    },
    addSubjective() {
      const q = {
        score: this.subjectiveScore,
        text: '',
        type: 2027,
        kindName: '主观题',
      }
      this.questions.push(q)
      this.$nextTick().then(() => {
        $(window).scrollTop(document.body.scrollHeight)
      })
    },
    serializeQuestion(q) {
      const data = {
        score: q.score,
        type: q.type,
        text: q.text,
      }
      if (q.type === 2027) return data
      data.answer = q.answer
      data.option_count = q.optionCount

      if (q.isTrueOrFalse) {
        data.isTrueOrFalse = q.isTrueOrFalse
      }
      if (q.multiAnswer) {
        data.multiAnswer = true
      }
      return data
    },
    createNq() {
      if (!this.title.length || this.title.length >= 100) {
        this.$toasted.error('互动资料名字必填且不能超过100个字符')
        return
      }
      if (!this.gradeId) {
        this.$toasted.error('请选择年级')
      }
      const answerSheet = []
      for (const q of this.questions) answerSheet.push(this.serializeQuestion(q))
      const params = {
        gradeId: this.gradeId,
        title: this.title,
        chapterId: this.chapterId,
        answerSheet: JSON.stringify(answerSheet),
        tmpMaterialFile: this.materialFileKey,
        materialFileType: this.materialFileType,
        tmpAnalysisFile: this.analysisFileKey,
        analysisFileType: this.analysisFileType,
        copyFromNq: this.copyFromNq,
      }

      axios.post('/pad/j/resource/newquestions/create/', qs.stringify(params)).then((response) => {
        if (response.data.status === 0) {
          this.$router.push({ name: 'mine-new-questions' })
        } else if (response.data.status === 1) {
          this.$toasted.error(response.data.detail)
        }
      })
    },
  },
}
</script>

<style lang="less">
.modal-large.modal-create-new-question {
  .modal-body {
    min-height: 55vh;
  }
  .modal-body-left {
    width: 28rem;
  }
  input[type=text], .ui.selection.dropdown {
    height: 2.4em;
    min-height: 2rem;
    padding: .5em 1em;
  }
}
</style>

<style lang="less" scoped>
@import "~assets/css/base-variables.less";
.new-question-container.preview {
  .seq-label-list .seq-label-outline:first-child {
    margin-left: 1rem;
  }
  .button.large.button {
    margin-top: .5em;
    margin-left: 1rem;
  }

}
.seq-label-outline {
  font-size: 12px;
  font-family: @base-font-family;
  display: inline-block;
  margin: .5em;
  width: 2.3em;
  line-height: 2.2em;
  text-align: center;
  border: solid 1px @color-gray;
  border-radius: 2em;
  cursor: pointer;
  &.seq-label-square {
    border-radius: 2px;
    width: 2em;
    line-height: 2em;
  }
  &.option-correct {
    background-color: @color-teal;
    color: white;
    border: solid 1px @color-teal;
    pointer-events: none;
  }
  &.seq-label-square.option-correct {
    pointer-events: auto;
  }

}

.note {
  font-size: 12px;
}
input[type=text], .ui.selection.dropdown {
  width: 25rem;
  height: 2.4em;
  min-height: 2rem;
  padding: .5em 1em;
}
@media (min-width: @max-width)
{
  .new-question-container {
    margin-left: 43rem;
    width: ~"calc(100% - 43rem)";
  }
  .add-questions {
    position: fixed;
    width: 63rem;
    bottom: 0;
    right: ~"calc(50vw - 748px)";
  }
  .sidebar-file {
    width: 42rem;
  }
}

@media (max-width: @max-width)
{
  .new-question-container {
    margin-left: 41rem;
    width: ~"calc(100% - 41rem)";
  }
  .add-questions {
    position: fixed;
    bottom: 0;
    left: 43rem;
    right: 2rem;
  }
  .sidebar-file {
    width: 40rem;
  }
}
.create-new-question.ui.form {
  .sidebar-file, .new-question-container {
    display: inline-block;
    background-color: white;
    box-shadow: 0 1px 1px rgba(0,0,0,0.15);
  }
  position: relative;
  .sidebar-file {
    height: 100vh;
    position: absolute;
    padding: .5rem;
    ul.text-muted {
      font-size: .9em;
      padding-left: 1rem;
    }
  }
  .sidebar-file.fixed {
    position: fixed;
    top: 0;
  }
  .new-question-container.preview .question-block{
    margin-bottom: 1rem;
  }
  .new-question-container {
    .actions {
      float: right;
      margin-top: 1.5rem;
    }
    .add-files {
      .file-row {
        margin: .8em 0;
      }
      .file-uploaded-label {
        font-weight: bold;
        font-size: .9em;
        color: #666;
      }

      .file-input-wrapper {
        display: inline-block;
        position: relative;
        input {
          display: none;
        }
        label {
          position: absolute;
          left: 0;
          top: 0;
          right: 0;
          bottom: 0;
          width: 100%;
        }
      }
    }
    padding-left: 1rem;
    padding-right: 1rem;
    min-height: 100vh;
    .container-title {
      .note {
        color: #888;
        font-size: 1rem;
      }
      padding-left: 0;
    }
    .field.inline label {
      width: 5rem;
    }

    input[type=number] {
      width: 3.5rem;
      padding: 2px 4px;
      margin: 0;
      border-radius: 0;
    }
    .add-questions {
      background-color: #ffffffee;
      border-top: 2px solid #42A5F5;
      box-shadow: 0 1px 6px rgba(0,0,0,.4), 0 -1px 2px 1px rgba(0,0,0,.2);
      padding: 1em;
      display: flex;
      .add-question {
        flex: 1;
        text-align: center;
      }
      .ui.button {
        box-shadow: 0 1px 1px rgba(0,0,0,0.15);
      }
      p {
        margin-top: 4px;
        font-size: 12px;
        margin-bottom: 0;
      }
    }
    .question-list {
      min-height: 20vh;
      padding-bottom: 10rem;
    }
    .question-block {
      width: 100%;
      margin-bottom: 1.5rem;
      .ui.mini.menu {
        box-shadow: none;
        border: none;
        background-color: #f8f8f8;
        min-height: 2.5em;
        margin-bottom: 0;
        border-radius: 0;
        input[type=number] {
          margin: -.5em .2em;
        }
        .right.menu {
          visibility: hidden;
        }
        .kind-name {
          font-weight: bold;
        }
      }
      input[type=text]{
        border-radius: 0;
        display: block;
        width: 100%;
        font-size: 12px;
      }
    }
    .question-block:hover {
      .ui.mini.menu .right.menu {
        visibility: visible;
      }
    }
  }
}


</style>



