<template>
  <div class="question-bank" id="question-bank">
    <div class="sidebar-tree">
      <span class="text-muted" v-if="showFavorited">收藏的题目不支持章节筛选</span>
      <div v-else>
        <span class="text-muted">根据章节筛选</span>
        <span class="link search link-primary" v-if="!searchMode" @click="searchMode=true"><i class="search icon"></i></span>
        <span class="link link-primary" v-if="searchMode" @click="searchMode=false">返回章节树</span>
        <v-select
          :on-search="searchChapter"
          :options="options"
          :on-change="searchChapterIdChanged"
          placeholder="输入关键词，搜索所有教材的章节"
          v-show="searchMode">
        </v-select>
        <chapter-selector bookSource="question" :onlyLeafCanSelect="false" @selectChapter="chapterId=>selectedChapterId=chapterId" key="question-chapter-selector" v-show="!searchMode" class="question-chapter-selector">
        </chapter-selector>
      </div>
      <div class="question-bucket">
        <div class="stat-labels">
          <span v-for="(count, qKind) in bucketKindCount" class="ui label" v-if="count">{{questionKinds[qKind]}}<div class="detail">{{count}}</div></span>
        </div>
        <button class="ui primary button" @click="showModalPaper">试题篮 [{{bucketQuestionCount}}] / 编辑预览</button>
      </div>
    </div>
    <div class="question-container" id="question-container">
      <div class="ui container-tab-menu menu">
        <a class="item" :class="showFavorited?'':'active'" @click="initBankQuestions">
          公共题库
        </a>
        <a class="item" :class="showFavorited?'active':''" @click="initFavoritedQuestions">
          我的收藏题目
        </a>
      </div>
      <div>
        <div v-if="teacher" class="pill-filter">
          题型：
          <span class="filter-label" :class="questionKind===0?'active':''" @click="questionKind=0">全部</span>
          <span v-for="qKind of subjectQKinds[teacher.subjectId]" @click="questionKind=qKind" class="filter-label" :class="questionKind===qKind?'active':''">{{questionKinds[qKind]}}</span>
        </div>
        <div class="pill-filter">
          难度：
          <span v-for="(value, key) in difficulties" @click="difficulty=parseInt(key)" class="filter-label"
           :class="parseInt(key)===difficulty?'active':''">{{value}}</span>
        </div>
      </div>
      <div class="question-list">
        <loader v-if="loading"></loader>
        <template v-else>
          <template v-for="question of questions">
            <div class="ui divider"></div>
            <div class="ui tiny right floated vertical buttons">
              <button class="ui orange basic button" @click="favorite(question)" v-if="!question.favorited"><i class="star icon"></i>收藏</button>
              <button class="ui yellow button tooltip" @click="undoFavorite(question)" v-else><i class="star icon"></i>已收藏</button>
              <button class="ui black basic button" v-if="checkInBucket(question)" @click="removeFromBucket(question)"><i class="remove circle icon"></i>移出组卷</button>
              <button class="ui blue basic button" v-else @click="addInBucket(question)"><i class="add circle icon"></i>加入组卷</button>
            </div>
            <component
              :is="currentView(question)"
              v-bind="{question: question, showStat: false}"
              :key="question.key"></component>
          </template>
        </template>
      </div>
      <div class="content-empty" v-if="!loading && questions.length===0">
        <img :src="require('assets/img/content-empty.png')">
        <p>
          什么也没有，试试<span class="text-danger">减少筛选条件</span>
        </p>
      </div>
      <pagination class="pagination"
        :totalPage="totalPage" :curPage="selectedPage"
        @update:selected="page=>fetchQuestions(page)" v-show="!loading && questions.length"></pagination>
    </div>
    <modal v-if="modalPaper" @close="modalPaper=false" class="modal-massive modal-paper" key="modal-paper">
      <h3 slot="header">编辑试卷：<span>共{{bucketQuestions.length}}大题，总分{{bucketTotalScore}}分</span></h3>
      <div slot="body">
        <div>
          <div class="modal-paper-sidebar">
            <table class="ui definition compact table">
              <thead>
                <tr class="center aligned">
                  <th></th>
                  <th>题型</th>
                  <th>小题分数</th>
                  <th>本题总分</th>
                </tr>
              </thead>
                <tbody>
                  <tr v-for="(q, idx) of bucketQuestions" class="center aligned">
                    <td>{{idx + 1}}</td>
                    <td>{{questionKinds[q.kind]}}</td>
                    <td> <input class="set-score" type="number" :value="q.subScore" v-on:input="($event)=>changeQuestionScore(q, $event)" min="0" max="100"></td>
                    <td>{{q.subScore * q.subQuestionCount}}</td>
                  </tr>
                  <tr class="center aligned">
                    <td></td>
                    <td>共{{bucketQuestions.length}}大题</td>
                    <td></td>
                    <td>总分{{bucketTotalScore}}分</td>
                  </tr>
                </tbody>
              </table>
          </div>
          <div class="question-list">
            <div v-for="(question, index) of bucketQuestions" class="question-block">
              <div class="ui mini menu">
                <a class="item">
                  每小题<input class="set-score" type="number" v-model.number="question.subScore" v-on:input="($event)=>changeQuestionScore(question, $event)" min="0" max="100">分 / 本题共{{question.subScore * question.subQuestionCount}}分
                </a>
                <div class="right menu">
                  <a class="item" :class="index===0?'disabled':''" @click="qForward(question)">
                    <i class="arrow up icon"></i>上移
                  </a>
                  <a class="item" :class="index===bucketQuestions.length-1?'disabled':''" @click="qBackward(question)">
                    <i class="arrow down icon"></i> 下移
                  </a>
                  <a class="item" @click="removeFromBucket(question)">
                    <i class="remove circle icon"></i>
                    移出组卷
                  </a>
                </div>
              </div>
              <component
                :is="currentView(question)"
                v-bind="{index: index + 1, question: question, showStat: false}"
                :key="question.key"></component>
            </div>
          </div>
        </div>
      </div>
      <div slot="footer">
        <button class="ui tiny blue button" @click="showModalPaperPublish">确定，下一步</button>
      </div>
    </modal>
    <modal v-if="modalPaperPublish" @close="modalPaperPublish=false" class="modal-large modal-paper modal-paper-publish" key="modal-paper-publish">
      <h3 slot="header">生成试卷：<span>共{{bucketQuestions.length}}大题，总分{{bucketTotalScore}}分</span></h3>
      <div slot="body">
        <form class="paper-form ui form">
          <div class="field">
            <label for="paper-title">试卷题目*</label>
            <input type="text" name="paper-title" v-model="paperTitle" placeholder="不超过100个字符">
          </div>
          <div class="field">
            <label>为本卷关联章节（可不填）</label>
            <chapter-selector bookSource="platform" key="platform-chapter-selector" @selectChapter="chapterId=>paperChapterId=chapterId"></chapter-selector>
          </div>
        </form>
      </div>
      <div slot="footer">
        <button class="ui tiny blue left floated button" @click="showModalPaper">上一步</button>
        <button class="ui tiny blue button" @click="createPaper($event)">生成试卷</button>
      </div>
    </modal>
  </div>
</template>

<script>
import axios from 'axios'
import { mapState } from 'vuex'
import _ from 'lodash'
import Pagination from 'components/pagination'
import ChapterSelector from 'components/chapter-selector'
import { getQuestionByData, QuestionKindToMV } from 'assets/js/questions/view-mode'
import { DIFFICULTIES, SUBJECTQKINDS, QUESTIONKINDS, QUESTIONSCORES } from 'assets/js/consts/common'
import Modal from 'components/modal'
import Loader from 'components/loader'

const qs = require('qs')

export default {
  name: 'question-bank',
  props: {},
  data() {
    return {
      loading: false,
      questionSource: 'bank',
      questions: [],
      totalPage: 0,
      selectedPage: 1,
      searchName: '',
      options: [],
      searchMode: false,
      selectedChapterId: 0,
      difficulties: DIFFICULTIES,
      difficulty: 0,
      subjectQKinds: SUBJECTQKINDS,
      questionKinds: QUESTIONKINDS,
      questionScores: QUESTIONSCORES,
      questionKind: 0,
      bucketQuestions: [],
      bucketKindCount: {},
      modalPaper: false,
      modalPaperPublish: false,
      paperTitle: '',
      paperChapterId: 0,
    }
  },
  components: {
    Pagination,
    Modal,
    ChapterSelector,
    Loader,
    vSelect: () => import(/* webpackChunkName: "vue-select" */ 'vue-select'),
  },
  mounted() {
    this.fetchQuestions()
    $(window).scroll(() => {
      const windowTop = $(window).scrollTop()
      if (windowTop >= 118) {
        $('.sidebar-tree').addClass('fixed')
      }
      if (windowTop < 118) {
        $('.sidebar-tree').removeClass('fixed')
      }
    })
  },
  watch: {
    selectedChapterId() {
      if (this.questionSource === 'bank') {
        this.fetchQuestions()
      }
    },
    questionSource() { this.fetchQuestions() },
    difficulty() { this.fetchQuestions() },
    questionKind() { this.fetchQuestions() },
    loading(val, oldVal) {
      if (val && !oldVal) {
        const windowTop = $(window).scrollTop()
        if (windowTop >= 118) {
          $(window).scrollTop(118)
        }
      }
    },
  },
  computed: {
    showFavorited() {
      return this.questionSource === 'favorited'
    },
    bucketQuestionCount() {
      return this.bucketQuestions.length
    },
    bucketTotalScore() {
      let score = 0
      for (const q of this.bucketQuestions) {
        score += (q.subScore * q.subQuestionCount || 0)
      }
      return score
    },
    ...mapState(['teacher']),
  },
  methods: {
    searchChapterIdChanged(val) {
      if (val) {
        this.selectedChapterId = val.value
      }
    },
    searchChapter(search, loading) {
      loading(true)
      this.getsearchedChpater(search, loading, this)
    },
    getsearchedChpater: _.debounce((search, loading, vm) => {
      axios.get('/pad/j/resource/chapters/search', { params: { name: search, source: 3 } }).then((response) => {
        if (response.data.status === 1){
          this.$toasted.error(response.data.detail, { duration: 1000, position: 'bottom-center' })
        } else if (response.data.status === 0) {
          vm.options = response.data.chapters
        }
        loading(false)
      })
    }, 250),
    changeQuestionScore(question, event) {
      const subScore = event.target.value
      if (subScore < 0) {
        event.target.value = 0
      } else if (subScore > 100) {
        event.target.value = 100
      }
      question.subScore = event.target.value
    },
    initBankQuestions() {
      this.questionKind = 0
      this.difficulty = 0
      this.questionSource = 'bank'
    },
    initFavoritedQuestions() {
      this.questionKind = 0
      this.difficulty = 0
      this.questionSource = 'favorited'
    },
    favorite(question) {
      if (question.favorited) return
      const params = {
        target_kind: question.kind,
        target_id: question.id,
      }
      axios.post('/pad/j/collections/', qs.stringify(params)).then((response) => {
        if (response.data.status === 0) {
          question.favorited = true
        } else {
          let info = '收藏失败。'
          if (response.data.detail) {
            info = `${response.data.detail}，收藏失败。`
          }
          this.$toasted.error(info, { duration: 1000, position: 'top-center' })
        }
      })
    },
    undoFavorite(question) {
      if (!question.favorited) return
      const params = {
        target_kind: question.kind,
        target_id: question.id,
      }
      axios.delete('/pad/j/collections/', { params }).then((response) => {
        if (response.data.status === 0) {
          question.favorited = false
        } else {
          let info = '取消收藏失败。'
          if (response.data.detail) {
            info = `${response.data.detail}，取消收藏失败。`
          }
          this.$toasted.error(info, { duration: 1000, position: 'top-center' })
        }
      })
    },
    showModalPaper() {
      if (this.bucketQuestionCount <= 0) {
        this.$toasted.error('当前试题篮没有任何题目，请先加些题。', {
          duration: 1000,
          position: 'bottom-center',
        })
        return
      }
      this.modalPaperPublish = false
      this.$nextTick(() => { this.modalPaper = true })
    },
    showModalPaperPublish() {
      if (this.bucketTotalScore > 300) {
        this.$toasted.error('组卷总分不能超过 300 分', {
          duration: 1000,
          position: 'bottom-center',
        })
        return
      }
      this.modalPaper = false
      this.$nextTick(() => { this.modalPaperPublish = true })
    },
    qForward(question) {
      const index = this.bucketIndexOf(question)
      if (index === 0) return
      this.bucketQuestions.splice(index, 1)
      this.bucketQuestions.splice(index - 1, 0, question)
    },
    qBackward(question) {
      const index = this.bucketIndexOf(question)
      if (index === this.bucketQuestions.length - 1) return
      this.bucketQuestions.splice(index, 1)
      this.bucketQuestions.splice(index + 1, 0, question)
    },
    addInBucket(question) {
      if (!this.checkInBucket(question)) {
        this.$set(question, 'subScore', this.questionScores[question.kind])
        this.bucketQuestions.push(question)
        if (this.bucketKindCount.hasOwnProperty(question.kind)) {
          this.bucketKindCount[question.kind] += 1
        } else {
          this.$set(this.bucketKindCount, question.kind, 1)
        }
      }
    },
    removeFromBucket(question) {
      const index = this.bucketIndexOf(question)
      if (index < 0) return

      if (this.bucketQuestionCount === 1 && this.modalPaper) {
        this.$toasted.show('这是卷子内最后一道题了，移出组卷后，编辑试卷的浮层将自动关闭。', {
          position: 'top-center',
          action: [
            {
              text: '不移出',
              onClick: (e, toastObject) => { toastObject.goAway(0) },
            },
            {
              text: '移出组卷',
              onClick: (e, toastObject) => {
                toastObject.goAway(0)
                this.bucketQuestions.splice(index, 1)
                this.bucketKindCount[question.kind] -= 1
                this.modalPaper = false
              },
            },
          ],
        })
      } else {
        this.bucketQuestions.splice(index, 1)
        this.bucketKindCount[question.kind] -= 1
      }
    },
    bucketIndexOf(question) {
      for (var i = 0, len = this.bucketQuestions.length; i < len; i++) {
        const q = this.bucketQuestions[i]
        if (q.key === question.key) {
          return i
        }
      }
      return -1
    },
    checkInBucket(question) {
      return this.bucketIndexOf(question) >= 0
    },
    currentView(question) {
      if (!QuestionKindToMV) return ''
      return QuestionKindToMV[question.kind].view
    },
    fetchQuestions(page = 1) {
      this.loading = true
      let url = '/pad/j/resource/questions'
      if (this.showFavorited) {
        url = '/pad/j/resource/questions/favorited'
      }

      return axios.get(url, { params: {
        page,
        pageSize: 15,
        chapterId: this.selectedChapterId,
        questionKind: this.questionKind,
        difficulty: this.difficulty,
      } }).then((response) => {
        this.questions = []
        for (const questionData of response.data.questions) {
          this.questions.push(getQuestionByData(questionData))
        }
        this.totalPage = response.data.totalPage
        this.selectedPage = page
        this.loading = false
      })
    },
    selectNodes(nodes) {
      if (nodes.length) {
        this.selectedChapterId = nodes[0].id
      } else {
        this.selectedChapterId = 0
      }
    },
    createPaper(event) {
      const paperTitleLength = this.paperTitle.length
      if (paperTitleLength <= 0 || paperTitleLength >= 100) {
        this.$toasted.error('试卷标题必填，且不能超过100个字符', {
          duration: 1000,
          position: 'top-center',
        })
      } else {
        $(event.target).addClass('loading')
        const questions = []
        for (const q of this.bucketQuestions) {
          questions.push([q.kind, q.id, q.subScore])
        }
        const params = {
          questions: JSON.stringify(questions),
          title: this.paperTitle,
          chapterId: this.paperChapterId,
          totalScore: this.bucketTotalScore,
        }
        axios.post('/pad/j/resource/papers/create', qs.stringify(params)).then((response) => {
          if (response.data.status === 1) {
            this.$toasted.error(response.data.detail, {
              duration: 1000,
              position: 'bottom-center',
              onComplete: () => {
                $(event.target).removeClass('loading')
              },
            })
          } else if (response.data.status === 0) {
            this.$toasted.success('创建成功，试卷已放入 备课->我的->我的试题组 中', {
              duration: 1000,
              position: 'bottom-center',
              onComplete: () => {
                this.bucketQuestions = []
                this.bucketKindCount = {}
                this.paperTitle = ''
                this.selectedChapterId = 0
                this.modalPaperPublish = false
                $(event.target).removeClass('loading')
                this.$router.push({ name: 'mine-papers' })
              },
            })
          }
        })
      }
    },
  },
}
</script>

<style lang="less">
@import "~assets/css/base-ui-component.less";
input.set-score {
  width: 3.5em;
  margin: -.5em .2em;
  line-height: 1.5;
}
.modal-paper.modal-paper-publish {
  .modal-body {
    overflow-y: scroll;
  }
  .paper-form {
    margin-left: 0;
    display: block;
    width: 28rem;
    min-height: 50vh;
  }
}
.modal-paper {
  .modal-header h3 span {
    font-size: 1rem;
  }
  .modal-paper-sidebar {
    margin: -1.2em 0;
    padding: 1.2em 0;
    width: 20rem;
    max-height: 80vh;
    display: block;
    position: fixed;
    overflow-y: auto;
  }
  .ui.compact.table th {
    padding: .5em;
  }
  .ui.table {
    font-size: 12px;
  }
  .question-list {
    display: inline-block;
    margin-left: 22rem;
    padding: 0;
    margin-top: -1rem;
  }
  .question-block {
    border: solid 1px transparent;
    padding: 1rem;
    .ui.mini.menu {
      box-shadow: none;
      border: none;
      background-color: #f8f8f8;
    }
  }
  .question-block:hover {
    border: solid 1px @color-blue;
    box-shadow: 0 1px 1px rgba(0,0,0,0.15);
  }
  .modal-footer {
    border-top: solid 1px #ddd;
    background-color: #f8f8f8;
  }
  .ui.tiny.loading.button:before {
    height: 1em;
    width: 1em
  }
  .ui.tiny.loading.button:after {
    height: 1em;
    width: 1em
  }
}
.v-select.loading {
  font-size: 13px;
  margin-top: .6rem;
  .spinner, .spinner:after {
    top: .8em;
    width: 1.5em;
    height: 1.5em;
  }
}
</style>

<style lang="less" scoped>
.container-tab-menu.menu {
  margin-bottom: 2rem;
}
.link.search.link-primary {
  line-height: 1.3;
  font-size: 1.2em;
  float: right;
  i.icon {
    margin-right: 0;
  }
}

.question-bank {
  .sidebar-tree, .question-container {
    display: inline-block;
    background-color: white;
    box-shadow: 0 1px 1px rgba(0,0,0,0.15);
  }
  position: relative;
  .sidebar-tree {
    height: 100vh;
    width: 25rem;
    position: absolute;
    padding: .5rem;
    .question-chapter-selector {
      height: 85vh;
    }
    .question-bucket {
      position: fixed;
      width: 24rem;
      bottom: 0;
      .button {
        width: 100%;
        font-weight: normal;
        font-size: 15px;
        padding: .6em 0;
      }
      .stat-labels {
        background-color: white;
        padding-top: .5em;
        .ui.label {
          margin-bottom: .5em;
          background-color: #f0f0f0;
          margin-left: 0;
          margin-right: .5em;
          padding: .4em 1em;
          font-size: 12px;
        }
      }
    }
  }
  .sidebar-tree.fixed {
    position: fixed;
    top: 0;
  }
  .question-container {
    min-height: 100vh;
    margin-left: 26rem;
    width: ~"calc(100% - 26rem)"
  }
}

.pill-filter {
  padding: 0 1rem;
}

.question-list {
  padding: 1rem;
}

.question-list {
  font-family: Palatino, Times, Arial, SimSun, "Microsoft YaHei", "微软雅黑", STHeiti, sans-serif;
  line-height: 1.8;
  height: 100%;
  font-size: 14px;
  overflow-y: auto;
  position: relative;
  min-height: 40vh;
}

.question-list .divider:first-child {
  display: none;
}

.question-list .ui.buttons {
  padding-left: 1em;
  .ui.button {
    padding: .5em;
    margin-bottom: .2em;
  }
}

.ui.label.tooltip {
  border: none;
}

</style>



