<template>
  <div>
    <loader v-if="loading"></loader>
    <div class="item-list" v-else>
      <template v-for="item of items">
        <div class="item-block">
          <div class="item-cover">
            <img class="icon thumbnai" :src="item.thumbnailUrl" v-if="item.thumbnailUrl" @click="previewItem=item">
            <template v-else>
              <i class="pad icon" :class="getItemIconClass(item)" @click="previewItem=item"></i>
            </template>
          </div>
          <div class="item-content" @click="previewItem=item">
            <div class="item-title"><a class="ui basic teal label" v-if="showNewLabel && hasNewLabel(item)">新</a> {{item.name}}</div>
            <div class="text-muted extra-text">
              <span class="text-muted" v-if="isItemMine(item) && !mixedUser">{{item.createdTime && item.createdTime.replace('T', ' ')}}</span>
              <span class="text-muted fade-label" v-if="showSize">{{sizeDisplay(item.fileSize)}}</span>
              <span class="text-muted fade-label" v-if="showGrade && item.gradeId">{{grades[item.gradeId]}}</span>
              <span class="text-muted fade-label" v-if="showUsage && item.usage">{{docUsageNameMap[item.usage]}}</span>
              <span class="text-muted fade-label" v-if="teacher && isItemMine(item) && mixedUser">我的</span>
              <span class="text-muted" v-if="showSharedLabel && item.shared"><i class="ui share alternate yellow icon"></i>共享中</span>
            </div>
            <div class="text-muted extra-text" v-if="showChapter && item.chapters.length">
              关联章节： {{item.chapters.map(c=>c.label).join(' / ')}}
            </div>
          </div>
          <div class="item-action" v-if="teacher">
            <div class="ui dropdown icon basic button">
              <i class="ellipsis horizontal icon"></i>
              <div class="left menu">
                <div class="item" @click="copyNq(item)" v-if="item.kind === 2025 || item.kind === 2035">
                  <i class="copy outline icon"></i>复制
                </div>
                <div class="item" @click="createNqBy(item)" v-if="canBeUsedAsNqFile(item)">
                  <i class="puzzle piece icon"></i>由此新建互动资料
                </div>
                <div class="item" @click="editItem=item" v-if="isItemMine(item)">
                  <i class="setting icon"></i>设置共享/标题
                </div>
                <div class="item" @click="deleteItem=item" v-if="isItemMine(item)" >
                  <i class="trash icon"></i>删除
                </div>
              </div>
            </div>
            <button class="ui icon basic button" @click="sendItem=item" v-if="teacher.courses.length" data-tooltip="发送" data-position="top center" data-variation="small" data-inverted="">
              <i class="send outline icon"></i>
            </button>
            <button class="ui icon basic button" @click="favorite(item)" v-if="!item.favorited" data-tooltip="收藏" data-position="top center" data-variation="small" data-inverted="">
              <i class="empty star icon"></i>
            </button>
            <button class="ui icon yellow basic button" @click="undoFavorite(item)" v-else data-tooltip="取消收藏" data-position="top center" data-variation="mini" data-inverted="">
              <i class="star icon"></i>
            </button>
            <button class="ui icon red basic button" v-if="checkInBucket(item)" @click="removeFromBucket(item)" data-tooltip="移出资源筐" data-position="top center" data-variation="small" data-inverted="">
              <i class="remove circle icon"></i>
            </button>
            <button class="ui icon add basic button" v-else @click="addInBucket(item)" data-tooltip="加入资源筐" data-position="top center" data-variation="small" data-inverted="">
              <i class="add circle icon"></i>
            </button>
          </div>
        </div>
      </template>
    </div>
    <div class="content-empty" v-if="!loading && items.length===0">
      <img :src="require('assets/img/content-empty.png')">
      <p>
        什么也没有，试试<span class="text-danger">改变或减少筛选条件</span>
      </p>
    </div>
    <pagination class="pagination"
        :totalPage="totalPage" :curPage="curPage"
        @update:selected="(page) => {$emit('update:selected', page)}"
        v-show="!loading && items.length"></pagination>
    <resource-preview :previewItem="previewItem"
      @close="previewItem=null"></resource-preview>
    <modal v-if="deleteItem" class="item-delete" :withHeader="false" @close="deleteItem=null">
      <div slot="body">
        <span class="ui text-center circular label">
          <i class="ui red warning icon"></i>
        </span>
        <p class="alert-text">确认要删除 <strong>{{deleteItem.name}}</strong> 吗？删除后不可恢复。</p>
      </div>
      <div slot="footer">
        <button class="ui basic button" @click="deleteItem=null">取消</button>
        <button class="ui red button" @click="actionDelItem(deleteItem)">删除</button>
      </div>
    </modal>
    <modal v-if="editItem" @close="editItem=null" class="item-edit">
      <h4 slot="header">编辑：<span>{{editItem.name}}</span></h4>
      <div slot="body">
        <form class="courseware-form ui form">
          <div class="field required">
            <label>名称</label>
            <div class="ui right labeled mini input" v-if="editItemExt">
              <input type="text" v-model="editItemName" placeholder="必填，不超过100个字符">
              <div class="ui basic label"> {{editItemExt}} </div>
            </div>
            <input type="text" v-model="editItemName" v-else>
          </div>
          <template v-if="[2030, 2018, 2025, 2035].includes(editItem.kind)">
            <div class="inline field">
              <label>共享到校本资源</label>
              <div class="ui toggle checkbox">
                <input type="checkbox" v-model="editItemShared">
                <label></label>
              </div>
            </div>
            <p v-if="showCancelShareText" class="text-danger extra-text">取消共享此文件到校本资源后，系统会对其他老师隐藏此文件。</p>
            <div class="required grouped fields" v-if="editItemShared && editItem.kind===2030">
              <label>设置文件用途</label>
              <div class="field">
                <div class="ui radio checkbox"  v-for="(name, key) in docUsageNameMap"  v-if="parseInt(key) > 0">
                  <input type="radio" :value="parseInt(key)" v-model="editItemUsage" :id="'doc-usage-' + key">
                  <label :for="'doc-usage-' + key">{{name}}</label>
                </div>
              </div>
            </div>
          </template>
        </form>
      </div>
      <div slot="footer">
        <button class="ui tiny blue button" @click="postEditItem" :class="editItemName.length?'':'disabled'">保存</button>
      </div>
    </modal>
    <file-sender :file="sendItem" :teacher="teacher" @close="sendItem=null"
      v-if="sendItem && teacher"></file-sender>
 </div>
</template>

<script>
import axios from 'axios'
import { mapState, mapGetters } from 'vuex'
import Modal from 'components/modal'
import Loader from 'components/loader'
import ResourcePreview from 'components/resource-preview'
import {
  GRADES,
  DOC_USAGE_NAME_MAP,
  EXT2FILECOMPONENT,
  K_NEW_QUESTION_BANK_PAPER,
  K_PAD_NEW_QUESTION,
  NQ_SUPPORTED_FILE_TYPES,
} from 'assets/js/consts/common'
import Pagination from 'components/pagination'
import FileSender from './file-sender'

const qs = require('qs')

export default {
  name: 'resource-list',
  props: {
    items: { type: Array, required: true },
    loading: { type: Boolean, default: false },
    showGrade: { type: Boolean, default: false },
    showSize: { type: Boolean, default: true },
    showUsage: { type: Boolean, default: true },
    showChapter: { type: Boolean, default: false },
    showSharedLabel: { type: Boolean, default: false },
    totalPage: { type: Number, default: 0 },
    curPage: { type: Number, default: 0 },
    mixedUser: { type: Boolean, default: true },
    showNewLabel: { type: Boolean, default: false },
  },
  data() {
    return {
      previewItem: null,
      sendItem: null,
      deleteItem: null,
      grades: GRADES,
      editItem: null,
      editItemExt: '',
      editItemName: '',
      editItemShared: false,
      editItemUsage: 0,
      unshareItem: null,
      usage: 0,
      docUsageNameMap: DOC_USAGE_NAME_MAP,
    }
  },
  components: {
    Modal, Loader, FileSender, Pagination, ResourcePreview,
  },
  watch: {
    loading: function (val) {
      if (!val) {
        const windowTop = $(window).scrollTop()
        if (windowTop >= 118) {
          $(window).scrollTop(118)
        }
        this.$nextTick(() => {
          $(this.$el.querySelectorAll('.ui.basic.dropdown.button')).dropdown({ action: 'hide' })
        })
      }
    },
    shareItem(val, oldVal) {
      if (oldVal && !val && this.usage) {
        this.usage = 0
      }
    },
    editItem(val, oldVal) {
      if (val && !oldVal) {
        let ext = ''
        let name = val.name
        if (this.editItem.kind === 2030) {
          ext = this.editItem.name.split('.').pop()
          if (EXT2FILECOMPONENT.hasOwnProperty(ext.toLowerCase())) {
            ext = `.${ext.toLowerCase()}`
            name = this.editItem.name.slice(0, -ext.length)
          } else {
            ext = ''
          }
        }
        this.changeEditInfo(name, ext, val.shared, val.usage)
      } else if (oldVal && !val) {
        this.changeEditInfo('', '', false, 0)
      }
    },
  },
  computed: {
    showCancelShareText() {
      if (this.editItem) {
        return this.editItem.shared && !this.editItemShared
      }
    },
    timeStrForNewItem() {
      const d = new Date()
      d.setMonth(d.getMonth() - 6)
      return d.toISOString()
    },
    ...mapState(['teacher']),
    ...mapGetters(['bucketIndexOf', 'checkInBucket', 'bucketResourceCount']),
  },
  methods: {
    hasNewLabel(item) {
      return item.createdTime && item.createdTime > this.timeStrForNewItem
    },
    canBeUsedAsNqFile(item) {
      return item.kind === 2030 && NQ_SUPPORTED_FILE_TYPES.includes(item.fileType)
    },
    copyNq(item) {
      if (item.kind === K_PAD_NEW_QUESTION) {
        this.$router.push({
          name: 'create-new-question',
          query: { copyFromNqId: item.id },
        })
      }
      if (item.kind === 2035) {
        const base = process.env.NODE_ENV === 'development'
          ? '/static/w/pages/question-bank.html'
          : '/pad/question-bank'
        window.location.href = `${base}/gallery?tree_type=chapter&id=${item.id}`
      }
    },
    createNqBy(item) {
      const urlRe = /^(http[s]?:\/\/)?([\w\.-]+)\/(.*)\/?$/
      const matches = item.fileUrl.match(urlRe)
      if (!(matches && matches.length === 4)) {
        this.$toasted.error('文件地址匹配失败', { duration: 800, position: 'bottom-center' })
      }

      this.$router.push({
        name: 'create-new-question',
        query: { sourceFileKey: matches[3], sourceFileType: item.fileType },
      })
    },
    isItemMine(item) {
      return item.teacherId === this.teacher.id
    },
    changeEditInfo(name, ext, shared, usage) {
      this.editItemName = name
      this.editItemExt = ext
      this.editItemShared = shared
      this.editItemUsage = usage
    },
    postEditItem() {
      const name = this.editItemName + this.editItemExt
      if (name.length === 0 || name.length >= 100) {
        this.$toasted.error('名称必填且不超过100个字符', { duration: 800, position: 'bottom-center' })
        return
      }
      if (this.editItemShared && !this.editItemUsage && this.editItem.kind === 2030) {
        this.$toasted.error('请选择文件用途', { duration: 800, position: 'bottom-center' })
        return
      }
      const params = {
        targetKind: this.editItem.kind,
        targetId: this.editItem.id,
        name: this.editItemName + this.editItemExt,
        shared: this.editItemShared,
        usage: this.editItemShared ? this.editItemUsage : 0,
      }
      axios.post('/pad/j/resource/edit', qs.stringify(params)).then((response) => {
        if (response.data.status === 0) {
          this.$toasted.info('修改成功', { duration: 800, position: 'bottom-center' })
          this.editItem.name = params.name
          this.editItem.shared = params.shared
          this.editItem.usage = params.usage
          this.editItem = null
        } else if (response.data.status === 1) {
          this.$toasted.error(response.data.detail, { duration: 800, position: 'bottom-center' })
        }
      })
    },
    actionDelItem(item) {
      const index = this.items.indexOf(item)
      if (index >= 0) {
        axios.delete(`/pad/j/materials/remove/${item.kind}/${item.id}`).then((response) => {
          if (response.data.status === 0) {
            this.items.splice(index, 1)
            this.deleteItem = null
          } else if (response.data.status === 1) {
            this.$toasted.error(response.data.detail, { duration: 800, position: 'top-center' })
          }
        })
      }
    },
    sizeDisplay(size) {
      if (size > 1000000) return `${(size / 1000000).toFixed(1)}MB`
      if (size > 1000) return `${(size / 1000).toFixed(1)}KB`
      return `${size}B`
    },
    favorite(item) {
      if (item.favorited) return
      const params = {
        target_kind: item.kind,
        target_id: item.id,
      }
      axios.post('/pad/j/collections/', qs.stringify(params)).then((response) => {
        if (response.data.status === 0) {
          item.favorited = true
        } else {
          let info = '收藏失败。'
          if (response.data.detail) {
            info = `${response.data.detail}，收藏失败。`
          }
          this.$toasted.error(info, { duration: 1000, position: 'top-center' })
        }
      })
    },
    undoFavorite(item) {
      if (!item.favorited) return
      const params = {
        target_kind: item.kind,
        target_id: item.id,
      }
      axios.delete('/pad/j/collections/', { params }).then((response) => {
        if (response.data.status === 0) {
          item.favorited = false
        } else {
          let info = '取消收藏失败。'
          if (response.data.detail) {
            info = `${response.data.detail}，取消收藏失败。`
          }
          this.$toasted.error(info, { duration: 1000, position: 'top-center' })
        }
      })
    },
    getItemIconClass(item) {
      if ([K_PAD_NEW_QUESTION, K_NEW_QUESTION_BANK_PAPER].includes(item.kind)) return `ft-${item.kind}`
      return `ft-${item.fileType}`
    },
    addInBucket(item) {
      if (item.kind === K_NEW_QUESTION_BANK_PAPER) {
        this.$toasted.error('新试题组暂不支持放入课件包', { duration: 800, position: 'bottom-center' })
      } else {
        this.$store.dispatch('addInBucket', item)
      }
    },
    removeFromBucket(item) {
      if (item.kind !== K_NEW_QUESTION_BANK_PAPER) {
        this.$store.dispatch('removeFromBucket', item)
      }
    },
  },
}
</script>
<style lang="less">
@import "~assets/css/file-icon.less";

.modal-header h3 span {
  font-size: 1rem;
}
.item-edit .modal-container .modal-body {
   min-height: 200px;
}

.extra-text {
  font-size: 12px;
  margin-top: 5px;
}
</style>

<style lang="less" scoped>
@color-blue-200: #90CAF9;

.item-delete {
  text-align: center;
}
.item-delete .ui.circular.label {
  border: solid 1px #dd4b39;
  border-radius: 3em;
  font-size: 2em;
  width: 1.8em;
  height: 1.8em;
  background-color: white;
  .ui.icon {
    margin: 0;
  }
}

.item-list {
  padding: 1rem 0;
}

.item-list {
  .item-block {
    border-top: solid 1px #f0f0f0;
    padding: 1em 2em;
    display: flex;
    align-items: top;

    &:hover, &:active, &:focus {
      background-color: #f6faff;
    }
    .item-cover {
      width: 4.5rem;
      max-width: 4.5rem;
      text-align: center;
      .icon.thumbnai {
        cursor: pointer;
        border-top: solid 1px #f6f6f6;
        border-right: solid 1px #f6f6f6;
        box-shadow: -3px 3px 2px -2px rgba(0, 0, 0, 0.15);
        max-height: 4.5rem;
        max-width: 4.5rem;
        border-radius: 3px;
      }
      .pad.icon {
        cursor: pointer;
        height: 3.5em;
        width: 3.5em;
        padding: .6em;
        border-radius: 3px;
        box-shadow: -1px 1px 2px rgba(0, 0, 0, 0.15);
      }
    }
    .item-content {
      flex: 1;
      padding: 1rem 0;
      margin: -1rem 1rem;
      cursor: pointer;
      &:hover {
        .item-title {
          color: #42A5F5;
        }
      }
      .item-title:active, .item-title:focus {
        color: #42A5F5;
      }
      .item-title .ui.label {
        padding: .2em;
        font-size: .8em;
        border-radius: 2px;
      }
    }

    .item-title {
      padding-bottom: 5px;
      font-size: 16px;
      color: #666;
    }
    .text-muted.fade-label {
      font-size: 12px;
      padding: 2px;
      margin-right: .5em;
    }
    .item-action {
      text-align: right;
      .ui.button {
        font-size: 1.2rem;
        margin-right: 0;
      }
      .ui.button[data-position="top center"][data-tooltip]:after {
        font-size: 11px;
        padding: .5em;
      }
      .ui.icon.basic.yellow.button {
        box-shadow: none !important;
      }
      .ui.icon.basic.button {
        padding: .5em .3em;
        visibility: hidden;
        box-shadow: none !important;
        &.yellow, &.red, &.add {
          visibility: visible;
        }
        &.active, &:hover, &:focus, &:active {
          background-color: transparent !important;
        }
      }
      .ui.dropdown .menu {
        border: solid 1px @color-blue-200;
        &>.item {
          color: #555;
          font-size: .9rem;
          padding: .8em 1em !important
        }
      }


    }
  }
  .item-block:hover {
    .ui.icon.basic.button {
      visibility: visible;
    }
  }
}
.item-list .item-block:first-child {
  border-top: none;
}

</style>



