<template>
  <div class="platform-document" id="platform-document">
    <div class="sidebar-tree">
      <div>
        <span class="text-muted">根据章节筛选</span>
        <chapter-selector
          key="sidebar-selector"
          class="sidebar-platform-selector"
          bookSource="platform"
          :mustSelectOne="true"
          @selectChapter="cid=>selectedChapterId=cid"
          :initFirstSelected="true"></chapter-selector>
      </div>
      <resource-bucket class="component-resource-bucket"></resource-bucket>
    </div>
    <div class="document-container" id="document-container">
      <h2 class="container-title">平台资源</h2>
      <div class="pill-filter">
        用途：
        <span class="filter-label" v-for="(name, key) in docUsageNameMap"
          :class="usageType===parseInt(key)?'active':''" @click="usageType=parseInt(key)">{{name}}</span>
      </div>
      <div class="pill-filter">
        类型：
        <span class="filter-label" :class="fileType===0?'active':''" @click="fileType=0">全部</span>
        <span class="filter-label" :class="fileType===2024?'active':''" @click="fileType=2024">PPT</span>
        <span class="filter-label" :class="fileType===2020?'active':''" @click="fileType=2020">文档</span>
        <span class="filter-label" :class="fileType===2022?'active':''" @click="fileType=2022">图片</span>
        <span class="filter-label" :class="fileType===2004?'active':''" @click="fileType=2004">视频</span>
        <span class="filter-label" :class="fileType===2023?'active':''" @click="fileType=2023">音频</span>
      </div>
      <resource-list :items="documents" :loading="loading" :totalPage="totalPage" :curPage="selectedPage"
        @update:selected="page=>fetchDocuments(page)"></resource-list>
      <template v-if="recommendDocuments.length">
        <p class="recommend-documents">相关资源推荐</p>
        <resource-list :items="recommendDocuments" :totalPage="1" :curPage="1" key="recommend-documents"></resource-list>
      </template>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import { DOC_USAGE_NAME_MAP } from 'assets/js/consts/common'
import ChapterSelector from 'components/chapter-selector'
import ResourceBucket from './resource-bucket'
import ResourceList from './resource-list'

export default {
  name: 'platform-document',
  props: {},
  data() {
    return {
      loading: false,
      usageType: 0,
      fileType: 0,
      documents: [],
      totalPage: 0,
      selectedPage: 1,
      selectedChapterId: 0,
      docUsageNameMap: DOC_USAGE_NAME_MAP,
      recommendDocuments: [],
    }
  },
  components: {
    ChapterSelector, ResourceList, ResourceBucket,
  },
  mounted() {
    $(window).scroll(() => {
      const windowTop = $(window).scrollTop()
      if (windowTop > 118) {
        $('.sidebar-tree').addClass('fixed')
      }
      if (windowTop < 118) {
        $('.sidebar-tree').removeClass('fixed')
      }
    })
  },
  watch: {
    usageType() { this.fetchDocuments() },
    fileType() { this.fetchDocuments() },
    selectedChapterId() { this.fetchDocuments() },
  },
  methods: {
    fetchDocuments(page = 1) {
      this.loading = true
      const url = '/pad/j/resource/documents/platform'
      return axios.get(url, { params: {
        page,
        pageSize: 15,
        chapterId: this.selectedChapterId,
        usageType: this.usageType,
        fileType: this.fileType,
      } }).then((response) => {
        if (response.data.status === 0) {
          this.documents = response.data.documents
          this.totalPage = response.data.totalPage
          this.selectedPage = page
          this.loading = false
          if (response.data.documents.length < 10) {
            axios.get('/pad/j/resource/documents/recommend', { params: {
              chapterId: this.selectedChapterId,
            } }).then((response) => {
              this.recommendDocuments = response.data.documents
            })
          } else {
            this.recommendDocuments = []
          }
        } else if (response.data.detail) {
          this.$toasted.error(response.data.detail, {
            duration: 1000,
            position: 'top-center',
          })
          this.loading = false
        }
      })
    },
  },
}
</script>

<style lang="less" scoped>
.platform-document {
  .sidebar-tree, .document-container {
    display: inline-block;
    background-color: white;
    box-shadow: 0 1px 1px rgba(0,0,0,0.15);
  }
  position: relative;
  .sidebar-tree {
    z-index: 10;
    height: 100vh;
    width: 25rem;
    position: absolute;
    padding: .5rem;
    .sidebar-platform-selector {
      height: 85vh;
    }
    .component-resource-bucket {
      box-shadow: -2px -3px 3px rgba(0,0,0,0.15);
      position: fixed;
      width: 25rem;
      margin-left: -.5rem;
      bottom: 0;
    }
  }
  .sidebar-tree.fixed {
    position: fixed;
    top: 0;
  }
  .document-container {
    position: relative;
    min-height: 100vh;
    margin-left: 26rem;
    width: ~"calc(100% - 26rem)"
  }
}
.recommend-documents {
  text-align: center;
  color: #777;
  margin-top: 5rem;
  border-bottom: dashed 1px #ddd;
}
</style>



