<template>
  <div class="platform-document" id="platform-document">
    <div class="sidebar-tree">
      <div>
        <span class="text-muted">名师视频：来自于新东方在线与学而思名师</span><br>
        <span class="text-muted">微课资源：按相关的专题与知识点精心准备与录制</span><br/>
        <span class="text-muted">全国名师：来自于全国名师精讲课程</span>
      </div>
      <resource-bucket class="component-resource-bucket"></resource-bucket>
    </div>
    <div class="document-container" id="document-container">
      <h2 class="container-title">名师视频 / 微课</h2>
      <div>
        <div class="search ui icon input">
          <input type="text" placeholder="搜索视频名称、知识点、老师等" v-model="searchText" @keyup.enter="fetchVideos(1)">
          <i class="ui search icon" @click="fetchVideos(1)"></i>
        </div>
        <div class="pill-filter">
          来源：
          <span class="filter-label" :class="source===-1?'active':''" @click="source=-1">全部</span>
          <span class="filter-label" :class="source===3?'active':''" @click="source=3">新东方在线</span>
          <span class="filter-label" :class="source===7?'active':''" @click="source=7">学而思</span>
          <span class="filter-label" :class="source===9?'active':''" @click="source=9">全国名师</span>
          <span class="filter-label" :class="source===8?'active':''" @click="source=8">微课</span>
        </div>
      </div>
      <resource-list
        :items="videos"
        :loading="loading"
        :showGrade="true"
        :totalPage="totalPage"
        :curPage="selectedPage"
        :showChapter="true"
        :showNewLabel="true"
        @update:selected="page=>fetchVideos(page)"></resource-list>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import ResourceBucket from './resource-bucket'
import ResourceList from './resource-list'

export default {
  name: 'video-bank',
  props: {},
  data() {
    return {
      loading: false,
      source: -1,
      videos: [],
      totalPage: 0,
      selectedPage: 1,
      searchText: '',
    }
  },
  components: { ResourceList, ResourceBucket },
  mounted() {
    this.fetchVideos()
    $(window).scroll(() => {
      const windowTop = $(window).scrollTop()
      if (windowTop > 118) {
        $('.sidebar-tree').addClass('fixed')
      }
      if (windowTop < 118) {
        $('.sidebar-tree').removeClass('fixed')
      }
    })
  },
  watch: {
    source() { this.fetchVideos() },
  },
  methods: {
    fetchVideos(page = 1) {
      this.loading = true
      const url = '/pad/j/resource/videos'
      return axios.get(url, { params: {
        page,
        pageSize: 15,
        searchText: this.searchText,
        source: this.source,
      } }).then((response) => {
        if (response.data.status === 0) {
          this.videos = response.data.videos
          this.totalPage = response.data.totalPage
          this.selectedPage = page
          this.loading = false
        } else if (response.data.detail) {
          this.$toasted.error(response.data.detail, {
            duration: 1000,
            position: 'top-center',
          })
          this.loading = false
        }
      })
    },
  },
}
</script>

<style lang="less" scoped>
.platform-document {
  .sidebar-tree, .document-container {
    display: inline-block;
    background-color: white;
    box-shadow: 0 1px 1px rgba(0,0,0,0.15);
  }
  position: relative;
  .sidebar-tree {
    z-index: 10;
    height: 100vh;
    width: 25rem;
    position: absolute;
    padding: .5rem;
    .sidebar-selector {
      height: 85vh;
    }
    .component-resource-bucket {
      box-shadow: -2px -3px 3px rgba(0,0,0,0.15);
      position: fixed;
      width: 25rem;
      margin-left: -.5rem;
      bottom: 0;
    }
  }
  .sidebar-tree.fixed {
    position: fixed;
    top: 0;
  }
  .document-container {
    position: relative;
    min-height: 100vh;
    margin-left: 26rem;
    width: ~"calc(100% - 26rem)"
  }
}
.ui.icon.search.input {
  float: right;
  margin-right: 2rem;
  width: 18rem;
  input {
    font-size: 12px;
    padding: 8px;
  }
  i.search.icon {
    cursor: pointer;
    pointer-events: auto;
  }
}



</style>



