<template>
  <div class="yhfa-document" id="yhfa-document">
    <div class="sidebar-tree">
      <div>
        <span class="text-muted">根据章节筛选</span>
        <chapter-selector key="sidebar-selector-yhfa" class="sidebar-selector"
          bookSource="yhfa"
          :mustSelectOne="true"
          @selectChapter="cid=>selectedChapterId=cid"
          :initFirstSelected="true"></chapter-selector>
      </div>
      <resource-bucket class="component-resource-bucket"></resource-bucket>
    </div>
    <div class="document-container" id="document-container">
      <h2 class="container-title">优化方案</h2>
      <resource-list :items="documents" :loading="loading"
        :totalPage="totalPage" :curPage="selectedPage"
        @update:selected="page=>fetchDocuments(page)"></resource-list>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import ChapterSelector from 'components/chapter-selector'
import Loader from 'components/loader'
import ResourceBucket from './resource-bucket'
import ResourceList from './resource-list'

export default {
  name: 'yhfa',
  props: {},
  data() {
    return {
      loading: false,
      documents: [],
      totalPage: 0,
      selectedPage: 1,
      selectedChapterId: 0,
    }
  },
  components: {
    ChapterSelector, Loader, ResourceList, ResourceBucket,
  },
  mounted() {
    $(window).scroll(() => {
      const windowTop = $(window).scrollTop()
      if (windowTop > 118) {
        $('.sidebar-tree').addClass('fixed')
      }
      if (windowTop < 118) {
        $('.sidebar-tree').removeClass('fixed')
      }
    })
  },
  watch: {
    selectedChapterId() { this.fetchDocuments() },
  },
  methods: {
    fetchDocuments(page = 1) {
      this.loading = true
      const url = '/pad/j/resource/documents/platform'
      return axios.get(url, { params: {
        page,
        pageSize: 15,
        chapterId: this.selectedChapterId,
      } }).then((response) => {
        if (response.data.status === 0) {
          this.documents = response.data.documents
          this.totalPage = response.data.totalPage
          this.selectedPage = page
          this.loading = false
        } else if (response.data.detail) {
          this.$toasted.error(response.data.detail, {
            duration: 1000,
            position: 'top-center',
          })
          this.loading = false
        }
      })
    },
  },
}
</script>

<style lang="less" scoped>
.yhfa-document {
  .sidebar-tree, .document-container {
    display: inline-block;
    background-color: white;
    box-shadow: 0 1px 1px rgba(0,0,0,0.15);
  }
  position: relative;
  .sidebar-tree {
    z-index: 10;
    height: 100vh;
    width: 25rem;
    position: absolute;
    padding: .5rem;
    .sidebar-selector {
      height: 85vh;
    }
    .component-resource-bucket {
      box-shadow: -2px -3px 3px rgba(0,0,0,0.15);
      position: fixed;
      width: 25rem;
      margin-left: -.5rem;
      bottom: 0;
    }
  }
  .sidebar-tree.fixed {
    position: fixed;
    top: 0;
  }
  .document-container {
    position: relative;
    min-height: 100vh;
    margin-left: 26rem;
    width: ~"calc(100% - 26rem)";
  }
}
</style>



