<template>
  <div class="mine-new-questions">
    <resource-list
      :items="items"
      :loading="loading"
      :showSize="false"
      :totalPage="totalPage"
      :curPage="selectedPage"
      :mixedUser="false"
      :showChapter="true"
      :showSharedLabel="true"
      @update:selected="page=>fetchitems(page)"></resource-list>
  </div>
</template>

<script>
import axios from 'axios'
import { mapState } from 'vuex'
import { K_NEW_QUESTION_BANK_PAPER } from 'assets/js/consts/common'
import ResourceList from '../resource-list'

export default {
  name: 'new-mine-papers',
  props: {},
  data() {
    return {
      loading: false,
      items: [],
      totalPage: 0,
      selectedPage: 1,
    }
  },
  components: { ResourceList },
  mounted() { this.fetchitems() },
  watch: {
    minePageChapterId() { this.fetchitems() },
    $route: 'fetchitems',
  },
  computed: {
    ...mapState(['minePageChapterId']),
  },
  methods: {
    fetchitems(page = 1) {
      this.loading = true
      const url = '/pad/j/resource/teacher/created'
      return axios.get(url, { params: {
        page,
        pageSize: 15,
        chapterId: this.minePageChapterId,
        resourceType: K_NEW_QUESTION_BANK_PAPER,
      } }).then((response) => {
        if (response.data.status === 0) {
          this.items = response.data.items
          this.totalPage = response.data.totalPage
          this.selectedPage = page
          this.loading = false
        } else if (response.data.detail) {
          this.$toasted.error(response.data.detail, {
            duration: 1000,
            position: 'top-center',
          })
          this.loading = false
        }
      })
    },
  },
}
</script>



