<template>
  <div class="resource-bucket-wrapper">
    <div class="resource-bucket">
      <div class="items" v-if="unfoldBucket && bucketResourceCount">
        <div v-for="d of bucketResources" class="resource-item">
          <i class="pad icon" :class="'ft-'+d.kind" @click="previewItem=d" v-if="d.kind===2025"></i>
          <i class="pad icon" :class="'ft-'+d.fileType" @click="previewItem=d" v-else></i>
          <p>{{d.name}}</p>
          <button class="ui tiny icon red basic button" v-if="checkInBucket(d)" @click="removeFromBucket(d)">
            <i class="remove circle icon"></i>
          </button>
        </div>
      </div>
      <div class="ui buttons">
        <button class="ui black button" @click="$store.commit('toggleBucket')">
          <i class="ui caret icon" :class="unfoldBucket?'down':'up'" v-if="bucketResourceCount"></i>
          资源筐 [{{bucketResourceCount}}]
        </button>
        <button class="ui blue button" @click="showModalCourseware">组课件包</button>
      </div>
    </div>
    <modal v-if="modalCourseware" @close="modalCourseware=false" class="modal-massive modal-courseware" key="modal-courseware">
      <h3 slot="header">组织课件包：<span>共{{ bucketResourceCount }}个资源</span></h3>
      <div slot="body">
        <div class="draggable-groups">
          <div class="draggable-group">
            <h3>课前预习</h3>
            <draggable v-model="bcDocs" :options="{draggable:'.resource-item', group:'cwdocs'}" class="before-class">
              <div v-for="d of beforeClassDocs" :key="d.id" class="resource-item">
                <i class="pad icon" :class="'ft-'+d.kind" @click="previewItem=d" v-if="d.kind===2025"></i>
                <i class="pad icon" :class="'ft-'+d.fileType" @click="previewItem=d" v-else></i>
                <p>{{d.name}}</p>
                <button class="ui tiny icon red basic button" v-if="checkInBucket(d)" @click="removeFromBucket(d)">
                  <i class="remove circle icon"></i>
                </button>
              </div>
            </draggable>
          </div>
          <div class="draggable-group">
            <h3>上课课件</h3>
            <draggable v-model="icDocs" :options="{draggable:'.resource-item', group:'cwdocs'}" class="in-class">
              <div v-for="d of inClassDocs" :key="d.id" class="resource-item">
                <i class="pad icon" :class="'ft-'+d.kind" @click="previewItem=d" v-if="d.kind===2025"></i>
                <i class="pad icon" :class="'ft-'+d.fileType" @click="previewItem=d" v-else></i>
                <p>{{d.name}}</p>
                <button class="ui tiny icon red basic button" v-if="checkInBucket(d)" @click="removeFromBucket(d)">
                  <i class="remove circle icon"></i>
                </button>
              </div>
            </draggable>
          </div>
          <div class="draggable-group">
            <h3>课后作业</h3>
            <draggable v-model="acDocs" :options="{draggable:'.resource-item', group:'cwdocs'}" class="after-class">
              <div v-for="d of afterClassDocs" :key="d.id" class="resource-item">
                <i class="pad icon" :class="'ft-'+d.kind" @click="previewItem=d" v-if="d.kind===2025"></i>
                <i class="pad icon" :class="'ft-'+d.fileType" @click="previewItem=d" v-else></i>
                <p>{{d.name}}</p>
                <button class="ui tiny icon red basic button" v-if="checkInBucket(d)" @click="removeFromBucket(d)">
                  <i class="remove circle icon"></i>
                </button>
              </div>
            </draggable>
          </div>
        </div>
      </div>
      <div slot="footer">
        <span class="note">可上下/左右拖动一个资源；「上课课件」中至少包含一个资源。</span>
        <button class="ui tiny blue button" @click="showModalCoursewarePublish">确定，下一步</button>
      </div>
    </modal>
    <modal v-if="modalCoursewarePublish" @close="modalCoursewarePublish=false" class="modal-large modal-courseware modal-courseware-publish" key="modal-courseware-publish">
      <h3 slot="header">创建课件包<span>共{{ bucketResourceCount }}个资源</span></h3>
      <div slot="body">
        <form class="courseware-form ui form">
          <div class="field">
            <label for="cw-title">名称*</label>
            <input type="text" name="cw-title" v-model="cwTitle" placeholder="不超过100个字符">
          </div>
          <div class="field">
            <label>为课件包关联章节（可不填）</label>
            <chapter-selector bookSource="platform" key="platform-chapter-selector" @selectChapter="chapterId=>cwChapterId=chapterId"></chapter-selector>
          </div>
        </form>
        <p class="form-note">注：生成的课件包会放入 <strong>上课->课件库</strong> 中</p>
      </div>
      <div slot="footer">
        <button class="ui tiny blue left floated button" @click="showModalCourseware">上一步</button>
        <button class="ui tiny blue button" @click="createCourseware($event)">生成课件包</button>
      </div>
    </modal>
  </div>
</template>


<script>
import axios from 'axios'
import { mapState, mapGetters } from 'vuex'
import ChapterSelector from 'components/chapter-selector'
import Modal from 'components/modal'
import { BEFORE_CLASS, IN_CLASS, AFTER_CLASS } from 'assets/js/consts/common'
import { pathConvert } from '@/assets/js/utils'

const qs = require('qs')

export default {
  name: 'resource-bucket',
  props: {},
  data() {
    return {
      selectedChapterId: 0,
      modalCourseware: false,
      modalCoursewarePublish: false,
      cwTitle: '',
      cwChapterId: 0,
    }
  },
  created() {
    this.$store.dispatch('initBucketResources')
  },
  components: {
    Modal,
    ChapterSelector,
    Draggable: () => import(/* webpackChunkName: "vuedraggable" */ 'vuedraggable'),
  },
  computed: {
    bcDocs: {
      get() { return this.beforeClassDocs },
      set(value) { this.$store.commit('updateBeforeClassDocs', value) },
    },
    icDocs: {
      get() { return this.inClassDocs },
      set(value) { this.$store.commit('updateInClassDocs', value) },
    },
    acDocs: {
      get() { return this.afterClassDocs },
      set(value) { this.$store.commit('updateAfterClassDocs', value) },
    },
    ...mapState(['bucketResources', 'beforeClassDocs', 'inClassDocs', 'afterClassDocs', 'unfoldBucket']),
    ...mapGetters(['bucketIndexOf', 'checkInBucket', 'bucketResourceCount']),
  },
  methods: {
    removeFromBucket(item) {
      const index = this.bucketIndexOf(item)
      if (index < 0) return

      if (this.bucketResourceCount === 1 && this.modalCourseware) {
        this.$toasted.show('这是最后一个资源，移出后，组织课件包的浮层将自动关闭。', {
          position: 'top-center',
          action: [
            {
              text: '不移出',
              onClick: (e, toastObject) => { toastObject.goAway(0) },
            },
            {
              text: '移出',
              onClick: (e, toastObject) => {
                toastObject.goAway(0)
                this.$store.dispatch('removeFromBucket', item)
                this.modalCourseware = false
              },
            },
          ],
        })
      } else {
        this.$store.dispatch('removeFromBucket', item)
      }
    },
    showModalCourseware() {
      if (this.bucketResourceCount <= 0) {
        this.$toasted.error('当前资源筐为空，请先加点什么。', {
          duration: 1000,
          position: 'bottom-center',
        })
        return
      }
      this.modalCoursewarePublish = false
      this.$nextTick(() => { this.modalCourseware = true })
    },
    showModalCoursewarePublish() {
      if (this.inClassDocs.length === 0) {
        this.$toasted.error('「上课课件」中至少包含一个课件。', {
          duration: 1000,
          position: 'bottom-center',
        })
      } else {
        this.modalCourseware = false
        this.$nextTick(() => { this.modalCoursewarePublish = true })
      }
    },
    createCourseware(event) {
      const cwTitleLength = this.cwTitle.length
      if (cwTitleLength <= 0 || cwTitleLength >= 100) {
        this.$toasted.error('名称必填，且不能超过100个字符', {
          duration: 1000,
          position: 'bottom-center',
        })
      } else {
        $(event.target).addClass('loading')
        const docs = []
        for (const doc of this.beforeClassDocs) { docs.push([doc.kind, doc.id, BEFORE_CLASS]) }
        for (const doc of this.inClassDocs) { docs.push([doc.kind, doc.id, IN_CLASS]) }
        for (const doc of this.afterClassDocs) { docs.push([doc.kind, doc.id, AFTER_CLASS]) }
        const params = {
          docs: JSON.stringify(docs),
          name: this.cwTitle,
          chapterId: this.cwChapterId,
        }

        axios.post('/pad/j/resource/coursewares/create', qs.stringify(params)).then((response) => {
          if (response.data.status === 1) {
            this.$toasted.error(response.data.detail, {
              duration: 1000,
              position: 'bottom-center',
            })
          } else if (response.data.status === 0) {
            this.$toasted.success('创建成功，课件包已放入 上课->课件库 中', {
              duration: 1000,
              position: 'bottom-center',
              onComplete: () => {
                this.$store.dispatch('clearBucket')
                this.modalCoursewarePublish = false
                $(event.target).removeClass('loading')
                window.location.href = pathConvert('teaching')
              },
            })
          }
        })
      }
    },
  },
}

</script>
<style lang="less">
@import "~assets/css/file-icon.less";
</style>

<style lang="less">
.modal-header h3 span {
  font-size: 1rem;
}
.modal-courseware.modal-courseware-publish {
  .modal-body {
    overflow-y: scroll;
  }
  .courseware-form {
    margin-left: 0;
    display: inline-block;
    width: 25rem;
    min-height: 50vh;
  }
  .form-note {
    float: right;
    width: 20rem;
    display: inline-block;
    color: #888;
  }
}
.draggable-group .resource-item, .resource-bucket .resource-item {
  display: flex;
  align-items: center;
  .pad.icon {
    max-width: 1.5rem;
    height: 1.5em;
    width: 1.5em;
    padding: 3px;
    border-radius: 2px;
  }
  p {
    flex: 1;
    font-size: 13px;
    margin: 0;
    padding: .6rem .5rem;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  .ui.icon.red.basic.button {
    box-shadow: none !important;
    padding: 0;
  }
}
.modal-courseware {
  span.note {
    font-size: 12px;
    color: #888;
    float: left;
  }
  .draggable-groups {
    display: flex;
    .draggable-group {
      flex: 1;
      margin: 0 1rem;
      max-width: 33%;
    }
    .in-class, .before-class, .after-class {
      background-color: #f8f8f8;
      box-shadow: -1px 1px 1px rgba(0,0,0,0.15);
      min-height: 30vh;
      max-height: 60vh;
      padding: .5rem;
    }
  }
  .modal-footer {
    border-top: solid 1px #ddd;
    background-color: #f8f8f8;
  }
}
</style>

<style lang="less" scoped>
.resource-bucket {
  .ui.buttons {
    width: 100%;
    font-weight: normal;
    padding: 0;
    .ui.button {
      padding: .8rem;
      border-radius: 0;
    }
    .ui.black.button {
      background-color: #3f4347;
      width: 60%;
    }
  }
  .items {
    max-height: 50vh;
    overflow-y: auto;
    border: solid 2px #3f4347;
    border-bottom: none;
    background-color: #f9f9f9;
    padding: .5em;
  }
}
</style>



