<template>
<modal @close="$emit('close')" class="modal-massive modal-file-sender" key="file-sender-modal">
  <h3 slot="header">发送资料：<span>{{file.name}}</span></h3>
  <div slot="body" class="modal-body-container">
    <span class="text-danger" v-if="isOldPaper">此为旧试题组类型，已经不支持发送给学生作答。请使用新题库和新试题组。</span>
    <form class="cm-form left ui form">
      <div class="inline field required">
        <label for="cm-title">作业名称</label>
        <input type="text" id="cm-title" v-model="cmTitle" placeholder="不超过100个字符">
      </div>
      <div class="inline field">
        <label>发布时间</label>
        <time-picker place-holder-text="立即发送"
          key="res-publish-time"
          v-model="publishTime"
          :minDate="minDate"
          :maxDate="maxDate"
          ></time-picker>
      </div>
      <div class="inline field" v-if="needSubmit">
        <label>定时收卷</label>
        <time-picker place-holder-text="未设置（可不填）"
          key="res-deadline"
          v-model="deadline"
          :minDate="minDate"
          :maxDate="maxDate"
          :initOffsetMinutes="10"></time-picker>
      </div>
      <div class="field required">
        <label>选择类型</label>
        <div>
          <div class="ui radio checkbox">
            <input type="radio" id="before-class-radio" :value="3" v-model="classKind">
            <label for="before-class-radio">课前预习</label>
          </div>
          <div class="ui radio checkbox">
            <input type="radio" id="in-class-radio" :value="2" v-model="classKind">
            <label for="in-class-radio">上课课件</label>
          </div>
          <div class="ui radio checkbox">
            <input type="radio" id="after-class-radio" :value="1" v-model="classKind">
            <label for="after-class-radio">课后练习</label>
          </div>
        </div>
      </div>
      <div class="field" v-if="needSubmit">
        <div class="ui checkbox">
          <input type="checkbox" id="cm-open-answer" tabindex="0" class="hidden" v-model="openAnswer">
          <label for="cm-open-answer">开放答案<span class="note text-muted">（学生提交后立即展示正确答案)</span></label>
        </div>
      </div>
      <div class="field">
        <label for="cm-note">备注(选填)</label>
        <input type="text" id="cm-note" v-model="note" placeholder="学生可见的作业备注，不超过100个字符">
      </div>
      <div class="field note">
        <strong>注：</strong><span class="text-muted">定时发送和定时收卷只支持15天之内的设置。</span>
      </div>
      <div class="field note">
        <strong>注：</strong><span class="text-muted">时间选择器：鼠标移动到年、月、小时、分钟上滚动可以快速更改值；分钟点击&#9650;&#9660;为加减5分钟，可输入值来精确设置。</span>
      </div>
    </form>
    <form class="cm-form right ui form">
      <div class="field">
        <label>选择发送目标
          <a
            @click="selectAllCourses()"
            class="link link-primary"
            v-if="Object.keys(targets).length > 1">（全选所有班级）</a></label>
        <span class="text-danger" v-if="isNewPaper">此为新试题组，目前老师只能在电脑端查看和批改该类型的作业。教师平板端适配即将上线。</span>
        <div v-for="(t, courseId) of targets" :key="courseId" class="course-block">
          <div class="ui course checkbox" >
            <input type="checkbox" tabindex="0" class="hidden" v-model="t.switchOn"
              :id="'course-cb-'+t.course.id">
            <label :for="'course-cb-'+t.course.id">{{t.course.name}}</label>
          </div>
          <div class="pill-filter" v-if="t.switchOn">
            <span class="filter-label" :class="t.sendType==='course'?'active':''"
              @click="changeSendType(t, 'course')">全班发送</span>
            <span class="filter-label" :class="t.sendType==='student'?'active':''"
              @click="changeSendType(t, 'student')">按学生发送</span>
            <span class="filter-label" :class="t.sendType==='group'?'active':''"
              @click="changeSendType(t, 'group')">按组发送</span>
          </div>
          <div class="student-selector" v-show="t.switchOn && t.sendType==='student'">
            <div class="ui mini inline active loader" v-show="t.loading"></div>
            <template v-show="!t.loading">
              <div class="ui mini checkbox" v-for="(s, key) in t.course.students">
                <input type="checkbox" tabindex="0" class="hidden" :value="s.id" v-model="t.studentIds"
                  :id="'student-cb-'+t.course.id+s.id">
                <label :for="'student-cb-'+t.course.id+s.id">{{s.name}}</label>
              </div>
            </template>
            <template v-if="!t.course.hasStudent && t.course.studentAttached">
              当前教学班级还没有学生（仍选择 按学生发送 将什么都不做）
            </template>
          </div>
          <div class="group-selector" v-show="t.switchOn && t.sendType==='group'">
            <div class="ui mini inline loader" v-show="t.loading"></div>
            <template v-show="!t.loading">
              <div class="ui mini checkbox" v-for="(g, key) in t.course.groups">
                <input type="checkbox" tabindex="0" class="hidden" :value="g.id" v-model="t.groupIds"
                  :id="'group-cb-'+t.course.id+g.id">
                <label :for="'group-cb-'+t.course.id+g.id">{{g.name}}</label>
              </div>
            </template>
            <template v-if="!t.course.hasGroup && t.course.groupAttached">
              当前教学班级还没有分组（仍选择 按组发送 将什么都不做）<a class="link link-primary" :href="'/pad/manage/courses/' + t.course.id" target="_blank">去设置分组</a>
            </template>
          </div>
        </div>
      </div>
    </form>
  </div>
  <div slot="footer">
    <button class="ui tiny blue button" @click="sendFile($event)">发送</button>
  </div>
</modal>
</template>

<script>
import Modal from 'components/modal'
import axios from 'axios'
import {
  AFTER_CLASS,
  K_NEW_QUESTION_BANK_PAPER,
  K_PAD_PAPER,
  K_PAD_NEW_QUESTION,
  COURSE_STATUS_NORMAL,
} from 'assets/js/consts/common'
import TimePicker from 'components/time-picker'

const qs = require('qs')

export default {
  name: 'file-sender',
  props: {
    // file 可能是文件 互动资料 试卷
    file: { type: Object, required: true },
    teacher: { type: Object, required: true },
  },
  data() {
    let teacherCourses = this.teacher.courses.concat(this.teacher.smCourses)
    teacherCourses = teacherCourses.filter(c => c.status === COURSE_STATUS_NORMAL)

    const targets = {}
    for (const c of teacherCourses) {
      targets[c.id] = {
        switchOn: false,
        course: c,
        loading: false,
        sendType: 'course', // 可以是 ['course', 'group', 'student']
        courseId: [c.id],
        groupIds: [],
        studentIds: [],
      }
    }
    return {
      cmTitle: this.file.name,
      note: '',
      targets,
      classKind: AFTER_CLASS,
      publishTime: null,
      deadline: null,
      openAnswer: false,
    }
  },
  components: {
    Modal,
    TimePicker,
  },
  computed: {
    minDate: () => new Date(),
    maxDate: () => {
      let max = new Date()
      max.setDate(max.getDate() + 15)
      return max
    },
    isNewPaper() {
      return this.file.kind === K_NEW_QUESTION_BANK_PAPER
    },
    isOldPaper() {
      return this.file.kind === K_PAD_PAPER
    },
    needSubmit() {
      return this.file.kind === K_PAD_NEW_QUESTION || this.file.kind === K_NEW_QUESTION_BANK_PAPER
    },
  },
  methods: {
    selectAllCourses() {
      for (const courseId in this.targets) {
        if (this.targets.hasOwnProperty(courseId)) {
          this.targets[courseId].switchOn = true
        }
      }
    },
    sendFile(event) {
      if (this.cmTitle.length <= 0 || this.cmTitle.length >= 100 || this.note.length >= 100) {
        this.$toasted.error('作业名称必填；名称和备注不能超过100个字符', { duration: 1000, position: 'bottom-center' })
        return
      }
      if (this.publishTime && this.deadline && this.publishTime >= this.deadline) {
        this.$toasted.error('收卷时间必须大于定时发布时间。', { duration: 1000, position: 'bottom-center' })
        return
      }
      $(event.target).addClass('loading')

      const sendTargetData = []
      for (const key in this.targets) {
        const target = this.targets[key]
        if (target.switchOn) {
          let ids = target.courseId
          if (target.sendType === 'group') ids = target.groupIds
          else if (target.sendType === 'student') ids = target.studentIds
          if (ids.length) {
            sendTargetData.push({
              sendType: target.sendType,
              ids,
              courseId: target.course.id,
            })
          }
        }
      }

      if (sendTargetData.length <= 0) {
        this.$toasted.error('没有可发送的目标。', { duration: 1000, position: 'bottom-center' })
        $(event.target).removeClass('loading')
        return
      }
      const now = new Date()
      if (this.publishTime <= new Date(now.getTime() + 1 * 60000)) {
        this.publishTime = null
      }

      const params = {
        targetKind: this.file.kind,
        targetId: this.file.id,
        name: this.cmTitle,
        note: this.note,
        sendTargetData: JSON.stringify(sendTargetData),
        classKind: this.classKind,
        publishTime: this.publishTime && new Date(this.publishTime),
        deadline: this.deadline && new Date(this.deadline),
        openAnswer: this.openAnswer,
      }

      axios.post('/pad/j/resource/cm/send', qs.stringify(params)).then((response) => {
        if (response.data.status === 1) {
          this.$toasted.error(response.data.detail, {
            duration: 1000,
            position: 'bottom-center',
            onComplete: () => {
              $(event.target).removeClass('loading')
            },
          })
        } else if (response.data.status === 0) {
          this.$toasted.success('作业已发送，可在「批改作业」页面查看详情。', {
            duration: 1000,
            position: 'bottom-center',
            onComplete: () => {
              $(event.target).removeClass('loading')
              this.$emit('close')
            },
          })
        }
      })
    },
    courseChecked(course) {
      return this.targets[course.id].switchOn
    },
    changeSendType(target, sendType) {
      target.sendType = sendType

      if (sendType === 'course') return
      const course = target.course
      if (sendType === 'student') {
        if (!course.hasStudent && !course.studentAttached) {
          target.loading = true
          this.$nextTick(() => {
            axios.get('/pad/j/teach/students', { params: { courseId: course.id } }).then((response) => {
              this.$store.commit('injectCourseStudents', { courseId: course.id, studentsData: response.data.students })
              target.loading = false
            })
          })
        } else {
          target.loading = false
        }
      } else if (sendType === 'group') {
        if (!course.hasGroup && !course.groupAttached) {
          target.loading = true
          this.$nextTick(() => {
            axios.get('/pad/j/teach/course/groups', { params: { courseId: course.id } }).then((response) => {
              this.$store.commit('injectCourseGroups', { courseId: course.id, groupsData: response.data.groups })
              this.loading = false
            })
          })
        } else {
          target.loading = false
        }
      }
    },
  },
}
</script>

<style lang="less">
@import "~assets/css/base-ui-component.less";
@import "~assets/css/flatpickr-override.less";
@import "~assets/css/semantic-override.less";
.send-time-picker {
  color: @color-grey-700;
  font-size: 13px;
}
.modal-file-sender {
  .modal-body-container {
    display: flex;
    margin: -1.2em -1em;
  }
  .cm-form.right {
    flex: 1;
    padding: 1.5em 1em;
  }
  .cm-form.left {
    width: 30rem;
    .note {
      font-size: 12px;
    }
    background-color: #fefefe;
    padding: 1.5em 1em;
    border-radius: 0;
    background-color: white;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, .2);
    min-height: 80vh;
  }
  .modal-footer {
    border-top: solid 1px #ddd;
    background-color: #f8f8f8;
  }
}
</style>

<style lang="less" scoped>

@color-grey-700: #616161;
.pill-filter {
  display: inline-block;
  margin: 0;
}
.ui.course.checkbox label {
  font-weight: bold;
  min-width: 10em;
}
.course-block {
  margin: 1em 0;
}
.student-selector, .group-selector {
  margin: .5em .5em 1.5em 2em;
  padding: .5em 1em;
  background-color: #f8f8f8;
}

.ui.mini.checkbox {
  min-width: 5.2em;
  margin: .5em .5em .5em 0;
  font-size: 13px;
  label {
    color: #666;
    padding-left: 1.5em;
  }
}
.ui.radio.checkbox {
  margin-right: 2em;
}


.ui.form.left .inline.field label {
  width: 5em;
  margin-right: .4em;
}
.ui.form input[type=text] {
  padding: .4em .8em;
  color: @color-grey-700;
  font-size: 13px;
}
.ui.form .inline.field input[type=text] {
  width: 22rem;
}
.ui.form .filed input[type=text] {
  width: 100%;
}

</style>



