<template>
  <div class="uploaded-files">
    <resource-list
      :items="documents"
      :loading="loading"
      :totalPage="totalPage"
      :curPage="selectedPage"
      :mixedUser="false"
      :showChapter="true"
      :showSharedLabel="true"
      @update:selected="page=>fetchDocuments(page)"></resource-list>
  </div>
</template>

<script>
import axios from 'axios'
import { mapState } from 'vuex'
import ResourceList from '../resource-list'

export default {
  name: 'uploaded-files',
  props: {},
  data() {
    return {
      loading: false,
      documents: [],
      totalPage: 0,
      selectedPage: 1,
    }
  },
  components: { ResourceList },
  mounted() {
    this.fetchDocuments()
  },
  watch: {
    minePageChapterId() {
      this.fetchDocuments(1)
    },
    uploadActionCount() {
      this.fetchDocuments(1)
    },
    $route: 'fetchDocuments',
  },
  computed: {
    ...mapState(['minePageChapterId', 'uploadActionCount']),
  },
  methods: {
    fetchDocuments(page = 1) {
      this.loading = true
      const url = '/pad/j/resource/teacher/uploaded'
      return axios.get(url, { params: {
        page,
        pageSize: 15,
        chapterId: this.minePageChapterId,
      } }).then((response) => {
        if (response.data.status === 0) {
          this.documents = response.data.documents
          this.totalPage = response.data.totalPage
          this.selectedPage = page
          this.loading = false
        } else if (response.data.detail) {
          this.$toasted.error(response.data.detail, {
            duration: 1000,
            position: 'top-center',
          })
          this.loading = false
        }
      })
    },
  },
}
</script>



