<template>
  <div class="paper-bank">
    <div class="sidebar-tree">
      <div>
        <pad-tree class="book-tree"
          :treeData="paperKinds"
          :onlyLeafCanSelect="false"
          :mustSelectOne="true"
          :selectedNodes="selectedNodes"
          :initFirstSelected="true"
          v-if="paperKinds.length"></pad-tree>
      </div>
      <resource-bucket class="component-resource-bucket"></resource-bucket>
    </div>

    <div class="document-container" id="document-container">
      <h2 class="container-title">试卷库</h2>
      <div>
        <div class="pill-filter" v-if="!isGenuine">
          年级：
          <span class="filter-label" :class="grade===0?'active':''" @click="grade=0">全部</span>
          <span class="filter-label" :class="grade===5?'active':''" @click="grade=5">高一</span>
          <span class="filter-label" :class="grade===4?'active':''" @click="grade=4">高二</span>
          <span class="filter-label" :class="grade===3?'active':''" @click="grade=3">高三</span>
        </div>
        <div class="pill-filter">
          地区：
          <span class="filter-label" :class="location===0?'active':''" @click="location=0">全部</span>
          <span class="filter-label" :class="location===100?'active':''" @click="location=100">全国</span>
          <span v-for="l in locations" class="filter-label" :class="location===l.id?'active':''" @click="location=l.id">{{l.name}}</span>
        </div>
        <div class="pill-filter">
          年份：
          <span class="filter-label" :class="year===0?'active':''" @click="year=0">全部</span>
          <template v-if="isGenuine">
            <span v-for="y in genuineYears" class="filter-label" :class="year===y?'active':''" @click="year=y">{{ y }}</span>
          </template>
          <template v-else>
            <span v-for="y in normalYears" class="filter-label" :class="year===y?'active':''" @click="year=y">{{ y }}</span>
          </template>
        </div>
      </div>

      <template>
        <div>
          <loader v-if="loading"></loader>
          <div class="item-list" v-else>
            <template v-for="item in items">
              <div class="item-block">
                <div class="item-cover" @click.prevent.stop="clickItem(item)">
                  <i class="pad icon" :class="'ft-'+item.fileType" @click=""></i>
                </div>
                <div class="item-content">
                  <div class="item-title" @click="clickItem(item)">{{item.name}}
                  </div>
                  <div v-if="item.isPackage" class="text-muted extra-text">
                    <span class="text-muted">共 {{item["children"].length}} 个文件</span>
                  </div>
                  <template v-if="item.isPackage && item.open">
                    <div v-for='child in item["children"]' class="package-child">
                      <div class="child-name" @click.prevent.stop="previewChild(child)">
                        {{child['name']}}
                      </div>
                      <div class="child-operation">
                        <button class="ui icon basic button" @click="sendFile(child)" data-tooltip="发送" data-position="top center" data-variation="small" data-inverted="">
                          <i class="send outline icon"></i>
                        </button>
                        <button class="ui icon red basic button" v-if="checkInBucket(child)" @click="removeFromBucket(child)" data-tooltip="移出资源筐" data-position="top center" data-variation="small" data-inverted="">
                          <i class="remove circle icon"></i>
                        </button>
                        <button class="ui icon add basic button" v-if="!checkInBucket(child)" @click="addInBucket(child)" data-tooltip="加入资源筐" data-position="top center" data-variation="small" data-inverted="">
                          <i class="add circle icon"></i>
                        </button>
                      </div>
                    </div>
                  </template>
                </div>
                <div class="item-action">
                  <button class="ui icon basic button" @click="clickItem(item)" v-if="item.isPackage && item.open" data-tooltip="收起" data-position="top center" data-variation="small" data-inverted="">
                    <i class="compress icon"></i>
                  </button>
                  <button class="ui icon basic button" @click="clickItem(item)" v-if="item.isPackage && !item.open" data-tooltip="展开" data-position="top center" data-variation="small" data-inverted="">
                    <i class="expand icon"></i>
                  </button>
                  <button class="ui icon basic button" @click="sendFile(item)" v-if="!item.isPackage && teacher.courses.length" data-tooltip="发送" data-position="top center" data-variation="small" data-inverted="">
                    <i class="send outline icon"></i>
                  </button>
                  <button class="ui icon red basic button" v-if="!item.isPackage && checkInBucket(item)" @click.stop.prevent="removeFromBucket(item)" data-tooltip="移出资源筐" data-position="top center" data-variation="small" data-inverted="">
                    <i class="remove circle icon"></i>
                  </button>
                  <button class="ui icon add basic button" v-if="!item.isPackage && !checkInBucket(item)" @click.stop.prevent="addInBucket(item)" data-tooltip="加入资源筐" data-position="top center" data-variation="small" data-inverted="">
                    <i class="add circle icon"></i>
                  </button>
                </div>
              </div>
            </template>
          </div>
          <div class="content-empty" v-if="!loading && items.length===0">
            <img :src="require('assets/img/content-empty.png')">
            <p>
              什么也没有，试试<span class="text-danger">改变或减少筛选条件</span>
            </p>
          </div>
          <pagination class="pagination"
            :totalPage="totalPage" :curPage="curPage"
            @update:selected="(page) => {setCurPage(page)}"
            v-show="!loading && items.length"></pagination>

          <resource-preview :previewItem="previewItem"
            @close="previewItem=null"></resource-preview>

          <file-sender :file="sendItem" :teacher="teacher" @close="sendItem=null"
            v-if="sendItem && teacher.courses.length"></file-sender>
       </div>
      </template>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import { mapState, mapGetters } from 'vuex'
import Pagination from 'components/pagination'
import ResourcePreview from 'components/resource-preview'
import Loader from 'components/loader'
import PadTree from 'components/pad-tree'
import ResourceBucket from './resource-bucket'
import FileSender from './file-sender'

export default {
  name: 'paper-bank',
  props: {},
  data() {
    return {
      loading: false,
      year: 0,
      grade: 0,
      location: 0,
      locations: [],
      paperKind: 100,
      paperKinds: [],
      totalPage: 0,
      items: [],
      curPage: 1,
      previewItem: null,
      sendItem: null,
      selectedNodes: [],
      isGenuine: false,
      genuineYears: [2017, 2016, 2014, 2013, 2012, 2011],
      normalYears: [2018, 2017, 2016],
    }
  },
  components: { ResourceBucket, FileSender, Pagination, ResourcePreview, Loader, PadTree },
  mounted() {
    this.fetchPaperMetadata()
    $(window).scroll(() => {
      const windowTop = $(window).scrollTop()
      if (windowTop > 118) {
        $('.sidebar-tree').addClass('fixed')
      }
      if (windowTop < 118) {
        $('.sidebar-tree').removeClass('fixed')
      }
    })
  },
  watch: {
    year() {
      this.curPage = 1
      this.fetchPapers()
    },
    paperKind() {
      this.curPage = 1
      this.fetchPapers()
    },
    location() {
      this.curPage = 1
      this.fetchPapers()
    },
    grade() {
      this.curPage = 1
      this.fetchPapers()
    },
    curPage() {
      this.fetchPapers()
    },
    selectedNodes(val) {
      if (val.length) {
        this.paperKind = val[0].id
        if (this.paperKind === 300) {
          this.isGenuine = true
        } else {
          this.isGenuine = false
        }
      }
    },
  },
  methods: {
    setCurPage(page) {
      this.curPage = page
    },
    sendFile(item) {
      this.sendItem = {
        kind: item.kind,
        id: item.id,
        name: item.name,
      }
    },
    addInBucket(item) {
      const target = {
        kind: item.kind,
        id: item.id,
        name: item.name,
        fileType: item.fileType,
      }
      this.$store.dispatch('addInBucket', target)
    },
    removeFromBucket(item) {
      const target = {
        kind: item.kind,
        id: item.id,
        name: item.name,
        fileType: item.fileType,
      }
      this.$store.dispatch('removeFromBucket', target)
    },
    clickItem(item) {
      if (item.isPackage) {
        item.open = !item.open
      } else {
        this.previewItem = {
          kind: 2030,
          fileType: item.fileType,
          fileUrl: item.fileUrl,
          name: item.name,
        }
      }
    },
    previewChild(item) {
      this.previewItem = {
        kind: 2030,
        fileType: item.fileType,
        fileUrl: item.fileUrl,
      }
    },
    fetchPapers() {
      this.loading = true
      const url = '/pad/j/resource/papers'
      return axios.get(url, { params: {
        page: this.curPage,
        year: this.year,
        grade: this.grade,
        location: this.location,
        paperKind: this.paperKind,
      } }).then((response) => {
        if (response.data.status === 0) {
          this.items = []
          response.data.papers.forEach((item) => {
            item.open = false
            this.items.push(item)
          })
          this.totalPage = response.data.totalPage
          this.loading = false
        } else if (response.data.detail) {
          this.$toasted.error(response.data.detail, {
            duration: 1000,
            position: 'top-center',
          })
          this.loading = false
        }
      })
    },
    fetchPaperMetadata() {
      const url = '/pad/j/resource/paper_metadata'
      return axios.get(url).then((response) => {
        if (response.data.status === 0) {
          this.locations = response.data.locations
          this.paperKinds = response.data.paperKinds
        }
      })
    },
  },
  computed: {
    ...mapState(['teacher']),
    ...mapGetters(['checkInBucket']),
  },
}
</script>

<style lang="less" scoped>
.item-list {
  padding: 1rem 0;
}
.item-list {
  .item-block {
    border-top: solid 1px #f0f0f0;
    padding: 1em 2em;
    display: flex;
    align-items: top;

    &:hover, &:active, &:focus {
      background-color: #f6faff;
    }
    .item-cover {
      width: 4.5rem;
      max-width: 4.5rem;
      text-align: center;
      .icon.thumbnai {
        cursor: pointer;
        border-top: solid 1px #f6f6f6;
        border-right: solid 1px #f6f6f6;
        box-shadow: -3px 3px 2px -2px rgba(0, 0, 0, 0.15);
        max-height: 4.5rem;
        max-width: 4.5rem;
        border-radius: 3px;
      }
      .pad.icon {
        cursor: pointer;
        height: 3.5em;
        width: 3.5em;
        padding: .6em;
        border-radius: 3px;
        box-shadow: -1px 1px 2px rgba(0, 0, 0, 0.15);
      }
    }
    .item-content {
      flex: 1;
      padding: 1rem 0;
      margin: -1rem 1rem;
      cursor: pointer;
      &:hover {
        .item-title {
          color: #42A5F5;
        }
      }
      .item-title:active, .item-title:focus {
        color: #42A5F5;
      }

      .package-child {
        display: flex;
        margin-top: 10px;
        font-size: 14px;
        width: 100%;

        .child-name {
          // width: 30em;
          color: #666;
          &:hover, &:active, &:focus {
            color: #42A5F5;
          }
        }

        .child-operation {
          flex: 1;
          text-align: right;
        }
      }
    }

    .item-title {
      padding-bottom: 5px;
      font-size: 16px;
      color: #666;
      .text-muted.fade-label {
        font-size: 12px;
        padding: 3px;
        margin-left: .5em;
      }
    }
    .item-action {
      text-align: right;
      .ui.button {
        font-size: 1.2rem;
        margin-right: 0;
      }
      .ui.button[data-position="top center"][data-tooltip]:after {
        font-size: 11px;
        padding: .5em;
      }
      .ui.icon.basic.yellow.button {
        box-shadow: none !important;
      }
      .ui.icon.basic.button {
        padding: .5em .3em;
        visibility: hidden;
        box-shadow: none !important;
        &.yellow, &.red, &.add {
          visibility: visible;
        }
        &:hover {
          background-color: transparent !important;
        }
      }
    }
  }
  .item-block:hover {
    .ui.icon.basic.button {
      visibility: visible;
    }
  }
}
.item-list .item-block:first-child {
  border-top: none;
}
.paper-bank {
  .sidebar-tree, .document-container {
    display: inline-block;
    background-color: white;
    box-shadow: 0 1px 1px rgba(0,0,0,0.15);
  }
  position: relative;
  .sidebar-tree {
    z-index: 10;
    height: 100vh;
    width: 25rem;
    position: absolute;
    padding: .5rem;
    .sidebar-selector {
      height: 85vh;
    }
    .component-resource-bucket {
      box-shadow: -2px -3px 3px rgba(0,0,0,0.15);
      position: fixed;
      width: 25rem;
      margin-left: -.5rem;
      bottom: 0;
    }
  }
  .sidebar-tree.fixed {
    position: fixed;
    top: 0;
  }
  .document-container {
    position: relative;
    min-height: 100vh;
    margin-left: 26rem;
    width: ~"calc(100% - 26rem)"
  }
}
.ui.icon.search.input {
  float: right;
  margin-right: 2rem;
  width: 18rem;
  input {
    font-size: 12px;
    padding: 8px;
  }
  i.search.icon {
    cursor: pointer;
    pointer-events: auto;
  }
}

</style>



